/* eslint-disable @typescript-eslint/no-unused-vars */
import { createSlice } from 'store/core/@reduxjs/toolkit';
import * as _ from 'lodash';
export interface RoomState {
  loading: boolean;
  error: any;
  success: any;
  rooms: any;
  roomsUltraSound: any[];
  roomsEndoscopy: any[];
  combineRooms: any[];
}

export const initialState: RoomState = {
  loading: false,
  error: null,
  success: {},
  rooms: [],
  roomsUltraSound: [],
  roomsEndoscopy: [],
  combineRooms: [],
};

const RoomFormSlice = createSlice({
  name: 'room',
  initialState,
  reducers: {
    getRoomAll(state) {
      state.loading = true;
      state.error = null;
    },

    getRoomAllSuccess(state, action: any) {
      state.loading = false;
      state.rooms = action.payload;
    },

    getRoomAllFailure(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    getRoomUltraSound(state) {
      state.loading = true;
      state.error = null;
    },

    getRoomUltraSoundSuccess(state, action: any) {
      state.loading = false;
      state.roomsUltraSound = action.payload;
    },

    getRoomUltraSoundFailure(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    getRoomEndoscopy(state) {
      state.loading = true;
      state.error = null;
    },

    getRoomEndoscopySuccess(state, action: any) {
      state.loading = false;
      state.roomsEndoscopy = action.payload;
    },

    getRoomEndoscopyFailure(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    getCombineRoom(state) {
      state.loading = true;
      state.error = null;
    },

    getCombineRoomSuccess(state, action: any) {
      state.loading = false;
      state.combineRooms = action.payload;
    },

    getCombineRoomFailure(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export const { actions, reducer, name: sliceKey } = RoomFormSlice;
