/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as DeptSelector from 'store/department/shared/selectors';
import { sliceKey, actions, reducer } from 'store/department/shared/slice';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { DepartmentSaga } from 'store/department/shared/saga';
import { useForm, Controller } from 'react-hook-form';
import { TEKDateTimeService } from 'services/datetime.service';
import { NumberService, Time } from 'services/number.service';
import Checkbox from '@material-ui/core/Checkbox';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import { areaClinci } from 'store/departmentService/constants/departmentService.constant';

const listHours: any = TEKDateTimeService.createListTimeInDay();
const listHoursAM: any = listHours.slice(5, 13);
const listHoursPM: any = listHours.slice(13, 17);
const selectHours = (start: number, end: number, listHours: any[]): any[] => {
  return listHours.slice(start, end);
};

const defaultValues = {
  name: null,
  code: null,
  address: null,
  areaCode: null,
  totalNumber: 0,
  startTimeActiveAM: null,
  endTimeActiveAM: null,
  startTimeActivePM: null,
  endTimeActivePM: null,
  isActive: true,
  listValueId: null,
};

const convertRequest = () => {
  return {
    type: 'TEK',
    displayOrder: 0,
    isSystem: true,
    description: '',
    ageTo: 90,
    ageFrom: 0,
    typeGender: 0,
  };
};

export function DeptActions({ open, onClose, valueRoomsType, valueNCK }) {
  const dispatch = useDispatch();
  useInjectReducer({ key: sliceKey, reducer });
  useInjectSaga({ key: sliceKey, saga: DepartmentSaga });
  const dept = useSelector(DeptSelector.selectDept);
  const {
    register,
    handleSubmit,
    errors,
    control,
    setValue,
    getValues,
  } = useForm({ defaultValues });
  const [timeEndAM, setTimeEndAM] = useState<any[]>(listHoursAM);
  const [timeEndPM, setTimeEndPM] = useState<any[]>(listHoursPM);
  const [minute, setMinute] = useState<number>(0);

  useEffect(() => {
    return () => {
      dispatch(actions.clearDepartment());
    };
  }, []);

  useEffect(() => {
    function initFormData(dept) {
      if (!_.isEmpty(dept)) {
        setValue('code', dept['code']);
        setValue('name', dept['name']);
        setValue('address', dept['address']);
        setValue('areaCode', dept['areaCode']);
        if (dept.department_config) {
          const {
            total_number,
            start_time_active_am,
            end_time_active_am,
            start_time_active_pm,
            end_time_active_pm,
            time_on_minute,
          } = dept['department_config'];
          setValue('totalNumber', total_number);
          setValue('isActive', dept?.is_active);
          setValue('startTimeActiveAM', start_time_active_am);
          setValue('endTimeActiveAM', end_time_active_am);
          setValue('startTimeActivePM', start_time_active_pm);
          setValue('endTimeActivePM', end_time_active_pm);
          setValue('areaCode', dept?.area_code);
          setValue('listValueId', dept?.list_value?.id);
          setMinute(time_on_minute || 0);
        }
      }
    }
    setTimeout(() => initFormData(dept));
  }, [dept]);

  const onSubmit = (values: any) => {
    if (_.isEmpty(dept)) {
      return dispatch(
        actions.createDept({
          ...values,
          ...convertRequest(),
          listValueTypeId: valueRoomsType.id,
          timeOnMinute: minute,
        }),
      );
    }
    dispatch(
      actions.updateDept({
        id: dept.id,
        dept: {
          ...values,
          ...convertRequest(),
          timeOnMinute: minute,
          display_order: dept.display_order || 0,
        },
      }),
    );
  };

  const autoMinutes = () => {
    try {
      const totalNumber = getValues('totalNumber');
      const AM: Time = {
        start: getValues('startTimeActiveAM'),
        end: getValues('endTimeActiveAM'),
      };
      const PM: Time = {
        start: getValues('startTimeActivePM'),
        end: getValues('endTimeActivePM'),
      };
      const { timeOnMinute }: any | Error = NumberService.autoGenerateNumber({
        total: totalNumber,
        AM,
        PM,
        msgError: '',
      });
      setMinute(timeOnMinute === Infinity ? 0 : timeOnMinute);
    } catch (error) {
      setMinute(0);
    }
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3 font-weight-bold">
            {!_.isEmpty(dept) ? 'Cp nht' : 'Thm'} Phng khm
          </h4>
          <div className="form-group">
            <label htmlFor="code">M phng khm</label>
            <input
              id="code"
              type="text"
              className="form-control Code"
              name="code"
              ref={register({ required: true })}
              placeholder="M"
              autoComplete="off"
            />
            {errors.code && errors.code?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng nhp m phng khm
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="name">Ni thc hin</label>
            <input
              id="name"
              type="text"
              className="form-control"
              name="name"
              ref={register({ required: true })}
              placeholder="Nhp ni thc hin"
              autoComplete="off"
            />
            {errors.name && errors.name?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng nhp ni thc hin
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="address">a ch</label>
            <input
              id="address"
              type="text"
              className="form-control"
              name="address"
              ref={register({ required: true })}
              placeholder="Nhp a ch"
              autoComplete="off"
            />
            {errors.address && errors.address?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>Vui lng nhp a ch</span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="areaCode">Khu phng khm</label>
            <select
              id="areaCode"
              className="form-control"
              name="areaCode"
              ref={register({ required: true })}
              placeholder="Nhp phng khm"
            >
              <option value="">Chn khu phng khm</option>
              {areaClinci.map(({ code, label }) => (
                <option key={code} value={code}>
                  {label}
                </option>
              ))}
            </select>
            {errors.areaCode && errors.areaCode?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng chn khu phng khm
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="listValueId">Nhm chuyn khoa</label>
            <select
              id="listValueId"
              className="form-control"
              name="listValueId"
              ref={register({ required: true })}
              placeholder="Nhp Nhm chuyn khoa"
            >
              <option value="">Chn nhm chuyn khoa</option>
              {!_.isEmpty(valueNCK) &&
                valueNCK.length &&
                valueNCK.map(({ id, description }) => (
                  <option key={id} value={id}>
                    {description}
                  </option>
                ))}
            </select>
            {errors.listValueId && errors.listValueId?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng chn nhm chuyn khoa
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="startTimeActiveAM">Thi gian bui sng</label>
            <div className="d-flex justify-content-between">
              <div className="d-block" style={{ width: '47%' }}>
                <select
                  className="form-control"
                  name="startTimeActiveAM"
                  ref={register({
                    required: true,
                  })}
                  onChange={({
                    target: { value },
                  }: React.ChangeEvent<HTMLSelectElement>) => {
                    autoMinutes();
                    setValue('endTimeActiveAM', null);
                    const startIndex = _.findIndex(
                      listHoursAM,
                      time => time.value === value,
                    );
                    if (startIndex > -1) {
                      setTimeEndAM(
                        selectHours(
                          startIndex + 1,
                          listHoursAM.length,
                          listHoursAM,
                        ),
                      );
                    }
                  }}
                >
                  <option value="">Bt u</option>
                  {listHoursAM
                    .slice(0, listHoursAM.length - 1)
                    .map((hour, idx) => (
                      <option value={hour.value} key={idx}>
                        {TEKDateTimeService.formatRenderTime(hour.value)}
                      </option>
                    ))}
                </select>
                {errors.startTimeActiveAM &&
                  errors.startTimeActiveAM?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>Chn gi bt u</span>
                  )}
              </div>
              <div className="d-block" style={{ width: '47%' }}>
                <select
                  className="form-control"
                  name="endTimeActiveAM"
                  ref={register({
                    required: true,
                  })}
                  onChange={autoMinutes}
                >
                  <option value="">Kt thc</option>
                  {timeEndAM.map((hour, idx) => (
                    <option value={hour.value} key={idx}>
                      {TEKDateTimeService.formatRenderTime(hour.value)}
                    </option>
                  ))}
                </select>
                {errors.endTimeActiveAM &&
                  errors.endTimeActiveAM?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>Chn gi kt thc</span>
                  )}
              </div>
            </div>
          </div>
          <div className="form-group">
            <label>Thi gian bui chiu</label>
            <div className="d-flex justify-content-between">
              <div className="d-block" style={{ width: '47%' }}>
                <select
                  className="form-control"
                  name="startTimeActivePM"
                  ref={register({
                    required: true,
                  })}
                  onChange={({
                    target: { value },
                  }: React.ChangeEvent<HTMLSelectElement>) => {
                    autoMinutes();
                    setValue('endTimeActivePM', null);
                    const startIndex = _.findIndex(
                      listHoursPM,
                      time => time.value === value,
                    );
                    if (startIndex > -1) {
                      setTimeEndPM(
                        selectHours(
                          startIndex + 1,
                          listHoursPM.length,
                          listHoursPM,
                        ),
                      );
                    }
                  }}
                >
                  <option value="">Bt u</option>
                  {listHoursPM
                    .slice(0, listHoursPM.length - 1)
                    .map((hour, idx) => (
                      <option value={hour.value} key={idx}>
                        {TEKDateTimeService.formatRenderTime(hour.value)}
                      </option>
                    ))}
                </select>
                {errors.startTimeActivePM &&
                  errors.startTimeActivePM?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>Chn gi bt u</span>
                  )}
              </div>
              <div className="d-block" style={{ width: '47%' }}>
                <select
                  className="form-control"
                  name="endTimeActivePM"
                  ref={register({
                    required: true,
                  })}
                  onChange={autoMinutes}
                >
                  <option value="">Kt thc</option>
                  {timeEndPM.map((hour, idx) => (
                    <option value={hour.value} key={idx}>
                      {TEKDateTimeService.formatRenderTime(hour.value)}
                    </option>
                  ))}
                </select>
                {errors.endTimeActivePM &&
                  errors.endTimeActivePM?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>Chn gi kt thc</span>
                  )}
              </div>
            </div>
          </div>
          <div className="form-group">
            <div className="d-flex justify-content-between">
              <div className="d-block" style={{ width: '47%' }}>
                <label htmlFor="totalNumber">Tng s lt</label>
                <input
                  type="number"
                  name="totalNumber"
                  className="form-control"
                  placeholder="Tng s lt"
                  ref={register({
                    required: true,
                    //min: 1,
                    max: 240,
                  })}
                  onChange={autoMinutes}
                />
                {errors.totalNumber &&
                  errors.totalNumber?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>Nhp tng s lt</span>
                  )}
                {/* https://jira.tekmedi.com/browse/CRBTC-986
                errors.totalNumber && errors.totalNumber?.type === 'min' && (
                  <span style={{ color: ' #fb0606' }}>
                    Tng s lt ti thiu l 1 lt
                  </span>
                )*/}
                {errors.totalNumber && errors.totalNumber?.type === 'max' && (
                  <span style={{ color: ' #fb0606' }}>
                    Tng s lt ti a l 240 lt
                  </span>
                )}
              </div>
              <div className="d-block" style={{ width: '47%' }}>
                <label htmlFor="timeOnMinute">S pht mi lt</label>
                <input
                  type="number"
                  name="timeOnMinute"
                  className="form-control"
                  value={minute}
                  disabled
                />
              </div>
            </div>
          </div>
          <div className="form-group">
            <label className="checkbox-inline" htmlFor="isActive">
              <Controller
                name="isActive"
                control={control}
                defaultValue={true}
                render={({ ...input }) => {
                  return (
                    <Checkbox
                      checked={input.value}
                      color="primary"
                      onChange={({ target: { checked } }) =>
                        setValue('isActive', checked)
                      }
                      inputProps={{ 'aria-label': 'secondary checkbox' }}
                    />
                  );
                }}
              />
              Trng thi
            </label>
          </div>
          <div className="form-group d-flex justify-content-end py-3 mb-0">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              ng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              {!_.isEmpty(dept) ? 'Cp nht' : 'Thm'}
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
