/* eslint-disable jsx-a11y/anchor-is-valid */

import { createSelector } from '@reduxjs/toolkit';
import { RootState } from 'store/core/types';
import { initialState } from './slice';

const selectDomain = (state: RootState) => state.deptGroup || initialState;

export const selectLoading = createSelector(
  [selectDomain],
  selectState => selectState.loading,
);

export const selectGroups = createSelector(
  [selectDomain],
  selectState => selectState.groups,
);

export const selectGroup = createSelector(
  [selectDomain],
  selectState => selectState.group,
);

export const selectDeptsInGroup = createSelector(
  [selectDomain],
  selectState => selectState.deptsInGroup,
);
