/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as DeptGroupSelector from 'store/departmentGroup/shared/selectors';
import * as DeptSelector from 'store/department/shared/selectors';
import * as GroupSlice from 'store/departmentGroup/shared/slice';
import * as DeptSlice from 'store/department/shared/slice';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { DeptGroupSaga } from 'store/departmentGroup/shared/saga';
import { DepartmentSaga } from 'store/department/shared/saga';
import { useForm, Controller } from 'react-hook-form';
import { configResponse } from 'utils/request';
import { Unsubscribe } from 'redux';
import { DeptGroupHttp } from 'store/departmentGroup/services/deptGroup.http';
import { RootStore } from 'store/configureStore';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CircularProgress from '@material-ui/core/CircularProgress';

const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxIcon fontSize="small" />;

export function DeptGroupActions({ open, onClose }) {
  const api = new DeptGroupHttp();
  const dispatch = useDispatch();
  useInjectReducer({
    key: GroupSlice.sliceKey,
    reducer: GroupSlice.reducer,
  });
  useInjectSaga({ key: GroupSlice.sliceKey, saga: DeptGroupSaga });

  useInjectReducer({ key: DeptSlice.sliceKey, reducer: DeptSlice.reducer });
  useInjectSaga({ key: DeptSlice.sliceKey, saga: DepartmentSaga });
  const group = useSelector(DeptGroupSelector.selectGroup);
  const deptList: any = useSelector(DeptSelector.selectListDept);
  const [openDrop, setOpenDrop] = useState<boolean>(false);
  const {
    register,
    handleSubmit,
    errors,
    control,
    setValue,
    setError,
    clearErrors,
  } = useForm();

  useEffect(() => {
    const getDeptInGroup = async (code: string) => {
      try {
        const groupDetps = await api.getDeptInGroup(code);
        let data = await configResponse(groupDetps);
        if (data && data.length) {
          data = data.map(({ code, name, department_virtual_code }) => ({
            code,
            name,
            department_virtual_code,
          }));
        }
        setValue('depts', data);
        const defaultRoom: any = deptList.data.find(
          room => room.code === _.get(_.head(data), 'department_virtual_code'),
        );
        if (defaultRoom) {
          setValue('departmentVirtualCode', defaultRoom);
        }
      } catch (err) {
        setValue('depts', []);
      }
    };
    if (!_.isEmpty(group)) {
      getDeptInGroup(group.code);
    }
  }, [group, deptList]);

  useEffect(() => {
    const rootStore$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload }: any = RootStore.getState().lastAction;
      switch (type) {
        case GroupSlice.actions.createGroupSuccess.type:
        case GroupSlice.actions.updateGroupSuccess.type:
          dispatch(GroupSlice.actions.addDeptToGroup(payload));
          break;
      }
    });
    dispatch(
      DeptSlice.actions.getDepts({
        pageNumber: 1,
        pageSize: 9999,
      }),
    );
    return () => {
      rootStore$();
      dispatch(GroupSlice.actions.clearGroup());
    };
  }, []);

  const onSubmit = (values: any) => {
    let deptsSelected: any[] = values['depts'];
    if (!deptsSelected.length) {
      return setError('depts', { type: 'required' });
    }
    deptsSelected = formatRequest(deptsSelected);
    if (_.isEmpty(group)) {
      return dispatch(
        GroupSlice.actions.createGroup({
          ...values,
          depts: deptsSelected,
        }),
      );
    }
    dispatch(
      GroupSlice.actions.updateGroup({
        ...values,
        id: group.id,
        depts: deptsSelected,
      }),
    );
  };

  const formatRequest = (groups: any[]) => {
    if (!groups.length) return [];
    return groups.map(({ code }) => code);
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3 font-weight-bold">Nhm phng khm</h4>
          <div className="form-group">
            <label htmlFor="groupCode">M nhm phng khm</label>
            <input
              id="groupCode"
              type="text"
              className="form-control Code"
              name="groupCode"
              ref={register({ required: true })}
              placeholder="M"
              autoComplete="off"
              defaultValue={!_.isEmpty(group) ? group.code : ''}
            />
            {errors.groupCode && errors.groupCode?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng nhp m nhm phng khm
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="groupName">Tn nhm phng khm</label>
            <input
              id="groupName"
              type="text"
              className="form-control"
              name="groupName"
              ref={register({ required: true })}
              placeholder="Nhp tn nhm phng khm"
              autoComplete="off"
              defaultValue={!_.isEmpty(group) ? group.description : ''}
            />
            {errors.groupName && errors.groupName?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng nhp tn nhm phng khm
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="departmentVirtualCode">Phng khm mc nh</label>
            <Controller
              name="departmentVirtualCode"
              control={control}
              defaultValue={null}
              rules={{ required: true }}
              render={({ field, ...input }: any) => {
                return (
                  <Autocomplete
                    value={input.value}
                    noOptionsText="Khng tm thy thng tin"
                    size="small"
                    getOptionSelected={(option: any, value) =>
                      option.code === value.code
                    }
                    getOptionLabel={option => option?.name}
                    options={
                      !_.isEmpty(deptList) && deptList.data.length
                        ? deptList.data
                        : []
                    }
                    renderOption={(option, { selected }) => {
                      return <React.Fragment>{option.name}</React.Fragment>;
                    }}
                    renderInput={params => (
                      <TextField
                        {...params}
                        variant="outlined"
                        placeholder="Tm kim phng"
                      />
                    )}
                    onChange={(e, val) => {
                      if (val && val.length) {
                        clearErrors('departmentVirtualCode');
                      }
                      setValue('departmentVirtualCode', val);
                    }}
                  />
                );
              }}
            />
            {errors.departmentVirtualCode &&
            errors.departmentVirtualCode?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng chn phng khm mc nh
              </span>
            ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="depts" className="mb-3">
              Danh sch phng khm
            </label>
            <Controller
              name="depts"
              control={control}
              defaultValue={[]}
              rules={{ required: true }}
              render={({ field, ...input }: any) => {
                return (
                  <Autocomplete
                    value={input.value}
                    disableCloseOnSelect
                    disableListWrap
                    multiple
                    loading={openDrop}
                    onOpen={() => setOpenDrop(true)}
                    onClose={() => setOpenDrop(false)}
                    limitTags={2}
                    noOptionsText="Khng tm thy thng tin"
                    size="small"
                    getOptionSelected={(option: any, value) =>
                      option.code === value.code
                    }
                    getOptionLabel={option => option?.name}
                    options={
                      !_.isEmpty(deptList) && deptList.data.length
                        ? deptList.data
                        : []
                    }
                    renderOption={(option, { selected }) => {
                      return (
                        <React.Fragment>
                          <Checkbox
                            icon={icon}
                            checkedIcon={checkedIcon}
                            style={{ marginRight: 8 }}
                            checked={selected}
                          />
                          {option.name}
                        </React.Fragment>
                      );
                    }}
                    renderInput={params => (
                      <TextField
                        {...params}
                        placeholder="Tm kim phng"
                        variant="outlined"
                        InputProps={{
                          ...params.InputProps,
                          endAdornment: (
                            <React.Fragment>
                              {openDrop ? (
                                <CircularProgress color="inherit" size={20} />
                              ) : null}
                              {params.InputProps.endAdornment}
                            </React.Fragment>
                          ),
                        }}
                      />
                    )}
                    onChange={(e, val) => {
                      if (val && val.length) {
                        clearErrors('depts');
                      }
                      setValue('depts', val);
                    }}
                  />
                );
              }}
            />
            {errors.depts && errors.depts?.type === 'required' ? (
              <span style={{ color: ' #fb0606' }}>
                Vui lng chn phng khm
              </span>
            ) : null}
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              ng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
