/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from 'store/core/@reduxjs/toolkit';

export interface DeptGroupState {
  loading?: boolean;
  success?: any;
  error?: any;
  groups: any[];
  group: any;
  deptsInGroup: any[];
}

export const initialState: DeptGroupState = {
  loading: false,
  success: null,
  error: null,
  groups: [],
  group: {},
  deptsInGroup: [],
};

const deviceFormSlice = createSlice({
  name: 'deptGroup',
  initialState,
  reducers: {
    getGroups(state, action) {
      state.loading = true;
    },

    getGroupsSuccess(state, action) {
      state.loading = false;
      state.groups = action.payload;
    },

    getGroupsFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getGroupById(state, action) {
      state.loading = true;
    },

    getGroupByIdSuccess(state, action) {
      state.loading = false;
      state.group = action.payload;
    },

    getGroupByIdFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getGroupByCode(state, action) {
      state.loading = true;
    },

    getGroupByCodeSuccess(state, action) {
      state.loading = false;
      state.group = action.payload;
    },

    getGroupByCodeFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    createGroup(state, action) {
      state.loading = true;
    },

    createGroupSuccess(state, action) {
      state.loading = false;
    },

    createGroupFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    updateGroup(state, action) {
      state.loading = true;
    },

    updateGroupSuccess(state, action) {
      state.loading = false;
    },

    updateGroupFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    deleteGroup(state, action) {
      state.loading = true;
    },

    deleteGroupSuccess(state, action) {
      state.loading = false;
    },

    deleteGroupFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    addDeptToGroup(state, action) {
      state.loading = true;
    },

    addDeptToGroupSuccess(state, action) {
      state.loading = false;
    },

    addDeptToGroupFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getDeptInGroup(state, action) {
      state.loading = true;
    },

    getDeptInGroupSuccess(state, action) {
      state.loading = false;
      state.deptsInGroup = action.payload;
    },

    getDeptInGroupFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    setGroup(state, action) {
      state.group = action.payload;
    },

    clearGroup(state) {
      state.group = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = deviceFormSlice;
