/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-fallthrough */
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Tooltip from '@material-ui/core/Tooltip';
import AppLoading from 'app/components/Loading';
import { ModalConfirm } from 'app/components/Modal/Confirm';
import STableBody from 'app/components/STableBody';
import STableHead from 'app/components/STableHead';
import TableHeaderSort from 'app/components/STableHeaderSort';
import * as _ from 'lodash';
import { useSnackbar } from 'notistack';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { Unsubscribe } from 'redux';
import { NotifyService } from 'services/notify.service';
import {
  rowsPerArray,
  useStyles,
} from 'store/common/constants/common.constant';
import { RootStore } from 'store/configureStore';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import * as DeptConst from 'store/department/constants/department.constant';
import { TableGroups } from 'store/department/constants/department.constant';
import { DepartmentSaga } from 'store/department/shared/saga';
import * as DeptSelector from 'store/department/shared/selectors';
import * as DeptSlice from 'store/department/shared/slice';
import { SettingManageType } from 'store/setting/constants/setting.constant';
import { SettingSaga } from 'store/setting/shared/saga';
import * as SettingSelector from 'store/setting/shared/selectors';
import * as SettingSlice from 'store/setting/shared/slice';
import { AppHelper, Order } from 'utils/app.helper';
import { DeptActions } from '../components/Department/DeptActions';

export class Config {
  [key: string]: any;
  constructor(config: any = null) {
    this.AM = {
      start_time_active_am: config ? config.start_time_active_am : '',
      end_time_active_am: config ? config.end_time_active_am : '',
    };
    this.PM = {
      start_time_active_pm: config ? config.start_time_active_pm : '',
      end_time_active_pm: config ? config.end_time_active_pm : '',
    };
    this.time_on_minute = config ? config.time_on_minute : 0;
    this.total_number = config?.total_number;
    this.used_number = config?.used_number;
    this.scheduler_number = config?.scheduler_number;
  }
}

const defaultPagi = {
  pageSize: 10,
  pageNumber: 1,
};

export function Department() {
  const dispatch = useDispatch();
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  useInjectReducer({ key: DeptSlice.sliceKey, reducer: DeptSlice.reducer });
  useInjectSaga({ key: DeptSlice.sliceKey, saga: DepartmentSaga });
  useInjectReducer({
    key: SettingSlice.sliceKey,
    reducer: SettingSlice.reducer,
  });
  useInjectSaga({ key: SettingSlice.sliceKey, saga: SettingSaga });
  const { register, handleSubmit, getValues, setValue } = useForm();
  const departments = useSelector(DeptSelector.selectListDept);
  const valueRoomsType = useSelector(SettingSelector.selectSettingType);
  const valueNCK = useSelector(SettingSelector.selectSpeciaList);
  const loading = useSelector(DeptSelector.selectLoading);
  const [openModal, setOpenModal] = useState<boolean>(false);
  const [confirmDel, setConfirmDel] = useState<boolean>(false);
  const [idDelete, setIdDelete] = useState<string>('');
  const [pagi, setPagi] = useState(defaultPagi);
  const pagiRef = useRef(pagi);
  useEffect(() => {
    pagiRef.current = pagi;
  }, [pagi]);

  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('name');
  const orderRef = useRef<Order>('asc');
  const orderByRef = useRef<string>('time');

  useEffect(() => {
    const rootStore$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload }: any = RootStore.getState().lastAction;
      switch (type) {
        case DeptSlice.actions.createDeptSuccess.type:
          notifyController
            .setNotifyMessage(
              DeptConst.RESPONSE_MESSAGE.CREATE_DEPARTMENT_SUCCESS,
            )
            .setSuccessNotify();
          regetListDept();
          break;
        case DeptSlice.actions.updateDeptSuccess.type:
          notifyController
            .setNotifyMessage(
              DeptConst.RESPONSE_MESSAGE.UPDATE_DEPARTMENT_SUCCESS,
            )
            .setSuccessNotify();
          dispatch(
            DeptSlice.actions.getDepts({
              ...getFormValue(),
              ...pagiRef.current,
              sort_field: orderByRef.current,
              sort_type: orderRef.current,
            }),
          );
          setOpenModal(false);
          setIdDelete('');
          setConfirmDel(false);
          break;
        case DeptSlice.actions.deleteDeptSuccess.type:
          notifyController
            .setNotifyMessage(
              DeptConst.RESPONSE_MESSAGE.DELETE_DEPARTMENT_SUCCESS,
            )
            .setSuccessNotify();
          regetListDept();
          break;
        case DeptSlice.actions.createDeptFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : DeptConst.RESPONSE_MESSAGE.DELETE_DEPARTMENT_FAIL,
            )
            .setFailNotify();
          break;
        case DeptSlice.actions.updateDeptFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : DeptConst.RESPONSE_MESSAGE.UPDATE_DEPARTMENT_FAIL,
            )
            .setFailNotify();
          break;
        case DeptSlice.actions.deleteDeptFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : DeptConst.RESPONSE_MESSAGE.DELETE_DEPARTMENT_FAIL,
            )
            .setFailNotify();
          regetListDept();
          break;
      }
    });
    dispatch(SettingSlice.actions.getManageType(SettingManageType.Department));
    dispatch(SettingSlice.actions.getSpecialist(SettingManageType.Specialist));
    dispatch(
      DeptSlice.actions.getDepts({
        ...getFormValue(),
        sort_field: orderByRef.current,
        sort_type: orderRef.current,
      }),
    );
    return () => {
      rootStore$();
    };
  }, []);

  const getFormValue = () => {
    return {
      code: getValues('code'),
      description: getValues('description'),
      page_size: pagi.pageSize,
      page_number: pagi.pageNumber,
    };
  };

  const regetListDept = () => {
    dispatch(DeptSlice.actions.getDepts({ ...getFormValue() }));
    setOpenModal(false);
    setIdDelete('');
    setConfirmDel(false);
  };

  const deleteDept = () => {
    if (idDelete === '') return;
    dispatch(DeptSlice.actions.deleteDept(idDelete));
  };

  const submitSearch = (values: any) => {
    dispatch(
      DeptSlice.actions.getDepts({
        code: values.code,
        description: values.description,
        page_size: pagi.pageSize,
        page_number: 1,
        sort_field: orderByRef.current,
        sort_type: orderRef.current,
      }),
    );
  };

  const handleReset = () => {
    setPagi(defaultPagi);
    setValue('code', null);
    setValue('description', null);
    setOrder('asc');
    setOrderBy('name');
    dispatch(
      DeptSlice.actions.getDepts({
        ...getFormValue(),
        page_size: defaultPagi.pageSize,
        page_number: defaultPagi.pageNumber,
        sort_field: 'name',
        sort_type: 'asc',
      }),
    );
  };

  const renderTableControl = () => {
    return (
      <form className="form-inline w-100" onSubmit={handleSubmit(submitSearch)}>
        <div className="row w-100">
          <div className="col-md-3 col-xl-3 pr-0">
            <input
              className="form-control mr-sm-2 w-100"
              type="code"
              name="code"
              placeholder="Tm theo m phng khm"
              ref={register()}
            />
          </div>
          <div className="col-md-3 col-xl-3 pr-0">
            <input
              className="form-control mr-sm-2 w-100"
              type="description"
              name="description"
              placeholder="Tm theo tn phng khm"
              ref={register()}
            />
          </div>
          <div className="col-md-6 col-xl-6 d-flex">
            <div className="btn-group">
              <button
                className="btn btn-outline-success btn__config mr-3"
                type="submit"
              >
                Tm kim <i className="fa fa-search"></i>
              </button>
            </div>
            <div className="btn-group">
              <button
                className="btn btn-primary btn__config mr-3"
                type="button"
                onClick={() => setOpenModal(true)}
              >
                Thm phng khm <i className="fa fa-plus"></i>
              </button>
              <button
                className="btn btn-warning btn__config"
                type="button"
                onClick={handleReset}
              >
                Reset <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const renderData = useMemo(() => {
    const { data = [], total_records, page_number, page_size } = departments;
    return {
      data,
      total: total_records,
      pageNumber: page_number,
      pageSize: page_size,
    };
  }, [departments]);

  const renderTable = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      /*
      const tableData: any[] = AppHelper.stableSort(
        renderData.data,
        AppHelper.getComparator(order, orderBy),
      );
      */
      const tableData: any[] = renderData.data;
      return tableData
        .map(item => {
          return {
            ...item,
            ...new Config(item?.department_config),
          };
        })
        .map((row, idx) => {
          const dept = _.cloneDeep(row);
          return (
            <TableRow key={idx} className="p-0">
              {TableGroups.map(column => {
                const value = dept[column.id];
                if (column.id === 'is_active') {
                  return (
                    <TableCell key={column.id} align="center">
                      <i
                        style={{
                          color: value ? '#4caf50' : '#ff0000',
                          fontSize: '16px',
                        }}
                        className={`fa fa-${value ? 'check' : 'times'}-circle`}
                        aria-hidden="true"
                      ></i>
                    </TableCell>
                  );
                }
                if (column.id === 'AM') {
                  if (
                    value &&
                    value['start_time_active_am'] === '' &&
                    value['end_time_active_am'] === ''
                  ) {
                    return (
                      <TableCell align="center" key={column.id}>
                        -
                      </TableCell>
                    );
                  }
                  return (
                    <TableCell key={column.id} align="center">
                      <Chip
                        color="primary"
                        size="small"
                        label={AppHelper.formatTimeSlots(value, [
                          'start_time_active_am',
                          'end_time_active_am',
                        ])}
                      />
                    </TableCell>
                  );
                }
                if (column.id === 'PM') {
                  if (
                    value &&
                    value['start_time_active_pm'] === '' &&
                    value['end_time_active_pm'] === ''
                  ) {
                    return (
                      <TableCell align="center" key={column.id}>
                        -
                      </TableCell>
                    );
                  }
                  return (
                    <TableCell key={column.id} align="center">
                      <Chip
                        color="primary"
                        size="small"
                        label={AppHelper.formatTimeSlots(value, [
                          'start_time_active_pm',
                          'end_time_active_pm',
                        ])}
                      />
                    </TableCell>
                  );
                }
                if (column.id === 'code') {
                  return (
                    <TableCell align="left" key={column.id}>
                      {value ? value : '-'}
                    </TableCell>
                  );
                }
                if (column.id === 'time_on_minute') {
                  return (
                    <TableCell align="center" key={column.id}>
                      {value ? value : '-'}
                    </TableCell>
                  );
                }
                if (
                  column.id === 'time_on_minute' ||
                  column.id === 'total_number' ||
                  column.id === 'used_number' ||
                  column.id === 'scheduler_number' ||
                  column.id === 'area_code'
                ) {
                  return (
                    <TableCell align="center" key={column.id}>
                      {value ?? '-'}
                    </TableCell>
                  );
                }
                if (column.id === 'actions') {
                  return (
                    <TableCell key={column.id}>
                      <Tooltip title={'Sa'} enterDelay={500} leaveDelay={100}>
                        <button
                          className="btn btn-primary btn-xs"
                          onClick={() => {
                            setOpenModal(true);
                            dispatch(DeptSlice.actions.getDeptById(dept.id));
                          }}
                        >
                          <i className="fa fa-pencil"></i>
                        </button>
                      </Tooltip>
                      <Tooltip title={'Xo'} enterDelay={500} leaveDelay={100}>
                        <button
                          className="btn btn-danger btn-xs mx-2"
                          onClick={() => {
                            setConfirmDel(true);
                            setIdDelete(dept.id);
                          }}
                        >
                          <i className="fa fa-trash-o"></i>
                        </button>
                      </Tooltip>
                    </TableCell>
                  );
                }
                if (column.id === 'queue_number') {
                  return (
                    <TableCell align="center" key={column.id}>
                      {value}
                    </TableCell>
                  );
                }
                return (
                  <TableCell key={column.id} align="left">
                    {value ? value : '-'}
                  </TableCell>
                );
              })}
            </TableRow>
          );
        });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={TableGroups.length}
          >
            Khng c d liu
          </TableCell>
        </TableRow>
      );
    }
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
    orderRef.current = isAsc ? 'desc' : 'asc';
    orderByRef.current = property;
    dispatch(
      DeptSlice.actions.getDepts({
        ...getFormValue(),
        ...pagiRef.current,
        sort_field: orderByRef.current,
        sort_type: orderRef.current,
      }),
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={TableGroups}
      />
    );
  };

  const handleChangePage = (event: unknown, page: number) => {
    let changePage = Object.assign({}, pagi, {
      pageNumber: page + 1,
    });
    setPagi(changePage);
    dispatch(
      DeptSlice.actions.getDepts({
        ...getFormValue(),
        ...changePage,
        sort_field: orderByRef.current,
        sort_type: orderRef.current,
      }),
    );
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, pagi, {
      pageSize: +event.target.value,
      pageNumber: 1,
    });
    setPagi(changeRowPerPage);
    dispatch(
      DeptSlice.actions.getDepts({
        ...getFormValue(),
        ...changeRowPerPage,
        sort_field: orderByRef.current,
        sort_type: orderRef.current,
      }),
    );
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hin Th"
          count={renderData.total}
          rowsPerPage={renderData.pageSize}
          page={renderData.pageNumber - 1}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading ? <AppLoading loading={true} /> : null}
      <ModalConfirm
        title={'Bn c chc chn mun xo phng khm khng?'}
        other={{
          bodyText: '',
        }}
        keyConfirm={''}
        open={confirmDel}
        onClose={() => setConfirmDel(false)}
        confirmMethod={() => deleteDept()}
      />
      {openModal && (
        <DeptActions
          valueRoomsType={valueRoomsType}
          valueNCK={valueNCK}
          open={openModal}
          onClose={() => setOpenModal(false)}
        />
      )}
      <div className="col-md-12">
        <div className="card card-topline-color-default">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <STableHead>{renderColumns()}</STableHead>
                    <STableBody>{renderTable()}</STableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
