import { createSlice } from 'store/core/@reduxjs/toolkit';

export interface DepartmentState {
  loading?: boolean;
  departments?: any;
  department?: any;
  error?: any;
  success?: any;
}

export const initialState: DepartmentState = {
  loading: false,
  departments: {},
  department: {},
  error: {},
  success: {},
};

const MedicineSlice = createSlice({
  name: 'departments',
  initialState,
  reducers: {
    getDepts(state, action) {
      state.loading = true;
    },

    getDeptsSuccess(state, action) {
      state.loading = false;
      state.departments = action.payload;
    },

    getDeptsFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getDeptById(state, action) {
      state.loading = true;
    },

    getDeptByIdSuccess(state, action) {
      state.loading = false;
      state.department = action.payload;
    },

    getDeptByIdFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    createDept(state, action) {
      state.loading = true;
    },

    createDeptSuccess(state, action) {
      state.loading = false;
    },

    createDeptFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    updateDept(state, action) {
      state.loading = true;
    },

    updateDeptSuccess(state, action) {
      state.loading = false;
    },

    updateDeptFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    deleteDept(state, action) {
      state.loading = true;
    },

    deleteDeptSuccess(state, action) {
      state.loading = false;
    },

    deleteDeptFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    clearDepartment(state) {
      state.department = {};
    },

    resetAll(state) {
      state.departments = {};
      state.error = {};
      state.success = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = MedicineSlice;
