/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useReducer, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as _ from 'lodash';
import * as AuthSelector from 'store/auth/shared/selectors';
import * as AuthSlice from 'store/auth/shared/slice';
import { AppHelper } from 'utils/app.helper';
import { Unsubscribe } from 'redux';
import { useSnackbar } from 'notistack';
import { RootStore } from 'store/configureStore';
import { NotifyService } from 'services/notify.service';

import { Table, Collapse, Switch, Button } from 'antd';
import 'antd/dist/antd.css';
import '../RoleDetail.css';
import {
  CheckSquareTwoTone,
  CloseOutlined,
  CheckOutlined,
  DeleteOutlined,
} from '@ant-design/icons';
import AppLoading from 'app/components/Loading';
import { ConfirmDeleteRole } from 'app/containers/PersonnelPage/components/ConfirmDeleteRole';
import { Link, useLocation } from 'react-router-dom';
import { ModalFindUser } from 'app/containers/PersonnelPage/components/ModalFindUser';

const { Panel } = Collapse;

interface ModalState {
  isAdd: boolean;
  isDelete: boolean;
  idDelete: string;
  isUser: boolean;
  isPermission: boolean;
  isFindUser: boolean;
}

enum ModalType {
  ADD = 'ADD',
  DELETE = 'DELETE',
  USER = 'USER',
  PERMISSION = 'PERMISSION',
  FINDUSER = 'FINDUSER',
}

type ModalAction = {
  type: ModalType;
  payload: any;
};

const UPDATE_PERMISSION_SUCCESS = 'Thay i quyn truy cp Thnh Cng';
const UPDATE_PERMISSION_FAIL = 'Thay i quyn truy cp tht bi';
const DELETE_USER_IN_ROLE_SUCCESS = 'Xo ngi dng khi role thnh cng';
const DELETE_USER_IN_ROLE_FAIL = 'Xo ngi dng khi role tht bi';

const initModalState: ModalState = {
  isAdd: false,
  isDelete: false,
  idDelete: '',
  isUser: false,
  isPermission: false,
  isFindUser: false,
};
const reduceModalState = (
  state: ModalState = initModalState,
  action: ModalAction,
): ModalState => {
  const { type, payload } = action;
  switch (type) {
    case ModalType.ADD:
      return {
        ...state,
        isAdd: payload,
      };
    case ModalType.PERMISSION:
      return {
        ...state,
        isPermission: payload,
        isUser: false,
      };
    case ModalType.USER:
      return {
        ...state,
        isUser: payload,
      };
    case ModalType.DELETE:
      const { id, isDelete } = payload;
      return {
        ...state,
        idDelete: id,
        isDelete: isDelete,
      };
    default:
      return state;
  }
};

export function RoleDetails() {
  const dispatch = useDispatch();
  const loading: any = useSelector<any>(AuthSelector.selectLoading);
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const defaultPermissions = useSelector(AuthSelector.selectPermissions);
  const role = useSelector(AuthSelector.selectRoleEditing);
  const roleEditing = useSelector(AuthSelector.selectRoleEditing);
  const [permissions, setPermissions] = useState<any[]>([]);
  const pagiOptions: number[] = [5, 10, 15, 30];
  const [pagi, setPagi] = useState<any>(() => ({
    rowsPerPage: _.first(pagiOptions),
    page: 0,
  }));
  const [keySearch, setKeySearch] = useState<string>('');
  const [listRender, setListRender] = useState<any>({});
  const [search, setSearch] = useState<boolean>(false);

  const idRole = useLocation();

  useEffect(() => {
    const idRoleConvert = idRole.search.split('=');
    dispatch(
      AuthSlice.actions.getRoleById({
        id: idRoleConvert[1],
        type: ModalType.PERMISSION,
      }),
    );
  }, []);

  useEffect(() => {
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case AuthSlice.actions.getRoleByIdSuccess.type:
          if (payload.type === ModalType.USER) {
            dispatchModal({
              type: ModalType.USER,
              payload: true,
            });
          }
          break;
        case AuthSlice.actions.deleteRoleFail.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          break;
        case AuthSlice.actions.deleteUserInRoleSuccess.type:
          notifyController
            .setNotifyMessage(DELETE_USER_IN_ROLE_SUCCESS)
            .setSuccessNotify();
          break;
        case AuthSlice.actions.updatePermissionInRoleSuccess.type:
          notifyController
            .setNotifyMessage(UPDATE_PERMISSION_SUCCESS)
            .setSuccessNotify();
          break;
        case AuthSlice.actions.updatePermissionInRoleFail.type:
          notifyController
            .setNotifyMessage(UPDATE_PERMISSION_FAIL)
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(roleEditing) && !_.isEmpty(defaultPermissions)) {
      const formatRoles = AppHelper.compareAndModifyTwoCollections(
        defaultPermissions,
        roleEditing.permissions,
        'id',
        'canActive',
      );
      setPermissions(formatRoles);
    }
  }, [roleEditing, defaultPermissions]);

  useEffect((): any => {
    if (!_.isEmpty(permissions) && permissions.length) {
      if (keySearch === '') {
        AppHelper.setPagiClient(permissions, pagi, setListRender);
      } else {
        AppHelper.setSearchPagiClient(
          permissions,
          pagi,
          keySearch,
          setListRender,
        );
      }
    }
  }, [permissions]);

  useEffect(() => {
    return () => {
      dispatch(AuthSlice.actions.clearEditRole());
    };
  }, []);

  const [modalState, dispatchModal] = useReducer(
    reduceModalState,
    initModalState,
  );

  const columnsPages: any = [
    {
      title: 'M trang',
      dataIndex: 'code',
      key: 'code',
      width: '35%',
    },
    {
      title: 'M t trang	',
      dataIndex: 'description',
      key: 'description',
      width: '35%',
    },
    {
      title: 'Trng thi trang',
      dataIndex: 'is_active',
      key: 'is_active',
      width: '10%',
      align: 'center',
      render: is_active => {
        return is_active ? (
          <CheckSquareTwoTone twoToneColor="#52c41a" />
        ) : (
          <CheckSquareTwoTone disabled />
        );
      },
    },
    {
      title: 'Chc nng',
      dataIndex: 'chucnang',
      key: 'chucnang',
      width: '20%',
      align: 'center',
      render: (row, idx) => {
        return idx.canActive ? (
          <Switch
            checkedChildren={<CheckOutlined />}
            style={{ backgroundColor: '#52c41a' }}
            defaultChecked
            onClick={(checked, event) => {
              changePermission(checked, idx);
            }}
          />
        ) : (
          <Switch
            unCheckedChildren={<CloseOutlined />}
            onClick={(checked, event) => {
              changePermission(checked, idx);
            }}
          />
        );
      },
    },
  ];

  const changePermission = (checked, idx) => {
    const updatedRole = Object.assign({}, idx, {
      canActive: checked,
    });
    const updateListRole = permissions.map(per => {
      if (per.id === updatedRole.id) {
        return updatedRole;
      }
      return per;
    });

    const permission_ids: string[] = updateListRole
      .filter(per => per.canActive)
      .map(({ id }) => id);

    dispatch(
      AuthSlice.actions.updatePermissionInRole({
        roleId: roleEditing.id,
        permission_ids,
      }),
    );
    setPermissions(updateListRole);
  };

  const deleteUserRole = (id: string) => {
    const activeUsers: string[] = role.users
      .filter(user => user.id !== id)
      .map(({ id }) => id);
    dispatch(
      AuthSlice.actions.deleteUserInRole({
        id: role.id,
        activeUser: activeUsers.length ? activeUsers : [],
        inActiveUser: [id],
      }),
    );
  };

  const columnsUsers: any = [
    {
      title: 'Tn ng nhp',
      dataIndex: 'user_name',
      key: 'user_name',
      width: '25%',
    },
    {
      title: 'M nhn vin',
      dataIndex: 'code',
      key: 'code',
      width: '25%',
    },
    {
      title: 'Tn nhn vin',
      dataIndex: 'full_name',
      key: 'full_name',
      width: '30%',
    },
    {
      title: 'Trng thi',
      dataIndex: 'is_active',
      key: 'is_active',
      width: '10%',
      align: 'center',
      render: is_active => {
        return is_active ? (
          <CheckSquareTwoTone twoToneColor="#52c41a" />
        ) : (
          <CheckSquareTwoTone twoToneColor="#3a3f38" />
        );
      },
    },
    {
      title: 'Chc nng',
      dataIndex: 'description',
      key: 'description',
      width: '10%',
      align: 'center',
      render: (row, idx) => {
        const id = idx.id;
        return (
          <Button
            className="ant-btn btn-danger btn-xs"
            icon={<DeleteOutlined />}
            onClick={() => deleteUserRole(id)}
          ></Button>
        );
      },
    },
  ];

  const renderPermissions = () => {
    if (!_.isEmpty(permissions)) {
      return (
        <Table
          columns={columnsPages}
          dataSource={permissions}
          pagination={false}
          rowKey={row => row.id}
          scroll={{ y: 600 }}
        />
      );
    }
  };

  const renderUsers = () => {
    if (!_.isEmpty(role)) {
      return (
        <Table
          columns={columnsUsers}
          dataSource={role.users}
          pagination={false}
          rowKey={row => row.id}
          scroll={{ y: 700 }}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading ? <AppLoading loading={true} /> : null}
      {search && <ModalFindUser open={true} onClose={() => setSearch(false)} />}
      <ConfirmDeleteRole
        title={'Bn c chc chn mun xo quyn truy cp Ny?'}
        id={modalState.idDelete}
        open={modalState.isDelete}
        onClose={() =>
          dispatchModal({
            type: ModalType.DELETE,
            payload: { isDelete: false, id: '' },
          })
        }
        confirmMethod={id => dispatch(AuthSlice.actions.deleteRole(id))}
      />
      <div className="col-12">
        <div className="card card-topline-color-default">
          <div className="card-head"></div>
          <div className="card-body ">
            <div className="table-scrollable">
              <Collapse defaultActiveKey={['1']}>
                <Panel header="Danh Sch Nhn Vin" key="1">
                  {renderUsers()}
                  <div className="text-right mt-2">
                    <Button type="primary" onClick={() => setSearch(true)}>
                      THM NHN VIN
                    </Button>
                  </div>
                </Panel>
                <Panel header="Danh Sch Trang Truy Cp" key="2">
                  {renderPermissions()}
                </Panel>
              </Collapse>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
