/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useRef } from 'react';
import * as _ from 'lodash';
import * as TableSelectors from 'store/table/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { sliceKey, reducer, actions } from 'store/table/shared/slice';
import { TableSaga } from 'store/table/shared/saga';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { Tooltip } from '@material-ui/core';
import { tablePatientReceiption } from 'store/table/constants/table.constant';
import { ModalTable } from '../components/TablePatientReceition/ModalTable';
import { ModalConfirm } from 'app/components/Modal/Confirm';
import { AppHelper, Order } from 'utils/app.helper';
import { deviceTypes, tableTypes } from 'store/table/constants/table.constant';
import {
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { RESPONSE_MESSAGE } from 'store/table/constants/table.constant';
import TablePagination from '@material-ui/core/TablePagination';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import AppLoading from 'app/components/Loading';
import TableHeaderSort from 'app/components/TableHeaderSort';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    minHeight: '200px',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export function TablePatientReceition() {
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  useInjectReducer({ key: sliceKey, reducer: reducer });
  useInjectSaga({ key: sliceKey, saga: TableSaga });
  const classes = useStyles();
  const dispatch = useDispatch();
  const tables: any = useSelector<any>(TableSelectors.selectTable);
  const loading: any = useSelector<any>(TableSelectors.selectLoading);
  const [openModal, setOpenModal] = useState<boolean>(false);
  const [openDelete, setOpenDelete] = useState<boolean>(false);
  const [idDelete, setIdDelete] = useState<string>('');
  const [filter, setFilter] = useState<any>(() => ({
    rowsPerPage: _.first(rowsPerArray),
    page: 0,
    search: '',
  }));
  const searchRef = useRef<any>(null);

  useEffect(() => {
    dispatch(actions.getTableAll(filter));
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case actions.getTableWithIdSuccess.type:
          setOpenModal(true);
          break;
        case actions.createTableSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.CREATE_TABLE_SUCCESS)
            .setSuccessNotify();
          setOpenModal(false);
          dispatch(actions.getTableAll(filter));
          break;
        case actions.updateTableSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.UPDATE_TABLE_SUCCESS)
            .setSuccessNotify();
          setOpenModal(false);
          dispatch(actions.getTableAll(filter));
          break;
        case actions.deleteTableSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.DELETE_TABLE_SUCCESS)
            .setSuccessNotify();
          dispatch(actions.getTableAll(filter));
          break;
        case actions.createTableFailure.type:
        case actions.updateTableFailure.type:
        case actions.deleteTableFailure.type:
          notifyController
            .setNotifyMessage(payload ? payload : ' xy ra li')
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  const deleteTable = () => {
    if (idDelete === '') return;
    dispatch(actions.deleteTable(idDelete));
  };

  const openDeleteTable = table => {
    setOpenDelete(true);
    setIdDelete(table.id);
  };

  const closeDialog = () => {
    setOpenDelete(false);
  };

  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('tekmedi_card_number');

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (e, page) => {
    let changePage = Object.assign({}, filter, { page });
    setFilter(changePage);
    dispatch(actions.getTableAll(changePage));
  };

  const handleChangeRowsPerPage = e => {
    let changeRowPerPage = Object.assign({}, filter, {
      rowsPerPage: +e.target.value,
      page: 0,
    });
    dispatch(actions.getTableAll(changeRowPerPage));
  };

  const searchTable = e => {
    let changeSearch = Object.assign({}, filter, {
      search: e.target.value,
    });
    setFilter(changeSearch);
    if (searchRef.current) {
      clearTimeout(searchRef.current);
    }
    searchRef.current = setTimeout(() => {
      dispatch(actions.getTableAll(changeSearch));
    }, 500);
  };

  const renderTableControl = () => {
    return (
      <div className="row">
        <div className="col-md-12">
          <div className="btn-group">
            <a
              id="addRow"
              className="btn btn-info btn__config"
              onClick={() => setOpenModal(true)}
            >
              Thm mi <i className="fa fa-plus"></i>
            </a>
          </div>
          <div className="btn-group ml-3">
            <label
              style={{
                alignItems: 'center',
                margin: 0,
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '90px' }}>Tm:</span>
              <input
                value={filter.search}
                onChange={searchTable}
                type="search"
                placeholder="Tm bn tip nhn"
                className="form-control form-control-sm"
                aria-controls="titleTable"
              />
            </label>
          </div>
        </div>
      </div>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tablePatientReceiption}
      />
    );
  };

  const renderTable = () => {
    if (!_.isEmpty(tables) && tables.data.length) {
      const tableData: any[] = AppHelper.stableSort(
        tables.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, idx) => {
        const card = _.cloneDeep(row);
        return (
          <TableRow key={row.id}>
            {tablePatientReceiption.map(column => {
              const value = card[column.id];
              if (column.id === 'no') {
                const { page, rowsPerPage } = filter;
                return (
                  <TableCell key={column.id}>
                    {page * rowsPerPage + idx + 1}
                  </TableCell>
                );
              } else if (column.id === 'device_type') {
                const device: any = deviceTypes.find(
                  dev => dev.value === value,
                );
                if (device) {
                  return <TableCell key={column.id}>{device.label}</TableCell>;
                } else {
                  return <TableCell key={column.id}>{value}</TableCell>;
                }
              } else if (column.id === 'type') {
                const table: any = tableTypes.find(tab => tab.value === value);
                if (table) {
                  return <TableCell key={column.id}>{table.label}</TableCell>;
                } else {
                  return <TableCell key={column.id}>{value}</TableCell>;
                }
              } else if (column.id === 'actions') {
                return (
                  <TableCell key={column.id}>
                    <Tooltip title={'Sa'} enterDelay={500} leaveDelay={100}>
                      <button
                        className="btn btn-primary btn-xs"
                        onClick={() => dispatch(actions.getTableWithId(row.id))}
                      >
                        <i className="fa fa-pencil"></i>
                      </button>
                    </Tooltip>
                    <Tooltip title={'Xo'} enterDelay={500} leaveDelay={100}>
                      <button
                        className="btn btn-danger btn-xs mx-2"
                        onClick={() => openDeleteTable(row)}
                      >
                        <i className="fa fa-trash-o"></i>
                      </button>
                    </Tooltip>
                  </TableCell>
                );
              }
              return (
                <TableCell key={column.id}>{value ? value : '-'}</TableCell>
              );
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tablePatientReceiption.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };

  const renderPagination = () => {
    if (!_.isEmpty(tables)) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={tables.recordsTotal}
          rowsPerPage={filter.rowsPerPage}
          page={filter.page}
          labelRowsPerPage={'Hin Th'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading && <AppLoading loading={true} />}
      {openModal && (
        <ModalTable open={true} onClose={() => setOpenModal(false)} />
      )}
      <ModalConfirm
        title={'Bn c chc chn mun xo bn ny?'}
        other={{
          bodyText: '',
        }}
        keyConfirm={''}
        open={openDelete}
        onClose={() => closeDialog()}
        confirmMethod={() => deleteTable()}
      />
      <div className="col-md-12">
        <div className="card card-topline-red">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
