import { TEKDateTimeService } from 'services/datetime.service';

export interface Time {
  start: any;
  end: any;
}

export interface AutoNumber {
  total: number;
  AM: Time;
  PM: Time;
  msgError?: string;
  setAutoNumber?: () => void;
}

const listHours: any = TEKDateTimeService.createListTimeInDay();
const listHoursAM: any = listHours.slice(5, 13);
const listHoursPM: any = listHours.slice(13, 17);
const defaultError: string = 'Tng s lt ln hn tng thi gian khm';

export class NumberService {
  static autoGenerateNumber({
    total,
    AM,
    PM,
    msgError,
  }: AutoNumber): any | Error {
    if (!total || !AM || !PM || !AM.start || !AM.end || !PM.start || !PM.end) {
      throw new Error('0');
    }
    const startAmMinutes = listHoursAM.findIndex(am => am.value === AM.start);
    const endAmMinutes = listHoursAM.findIndex(am => am.value === AM.end);
    const totalMinutesAm = (endAmMinutes - startAmMinutes) * 60;
    const startPmMinutes = listHoursPM.findIndex(am => am.value === PM.start);
    const endPmMinutes = listHoursPM.findIndex(am => am.value === PM.end);
    const totalMinutesPm = (endPmMinutes - startPmMinutes) * 60;
    const totalMinutes = totalMinutesAm + totalMinutesPm;
    if (totalMinutes < total) {
      throw new Error(
        msgError !== '' ? msgError : `${defaultError} ${totalMinutes} pht`,
      );
    }
    return {
      totalMinutes,
      timeOnMinute: Math.floor(totalMinutes / total),
    };
  }
}
