import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { configResponse } from 'utils/request';
import { get } from 'lodash';
import { DepartmentHttp } from '../services/department.http';
const api = new DepartmentHttp();

export function* getDepts(api, action) {
  const response = yield call(api.getDepts, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.getDeptsSuccess(data));
  } catch (error) {
    yield put(actions.getDeptsFailure(get(error, 'message')));
  }
}

export function* getDeptById(api, action) {
  const response = yield call(api.getDeptById, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.getDeptByIdSuccess(data));
  } catch (error) {
    yield put(actions.getDeptByIdFailure(get(error, 'message')));
  }
}

export function* createDept(api, action) {
  const response = yield call(api.createDept, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.createDeptSuccess(data));
  } catch (error) {
    yield put(actions.createDeptFailure(get(error, 'message')));
  }
}

export function* updateDept(api, action) {
  const response = yield call(api.updateDept, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.updateDeptSuccess(data));
  } catch (error) {
    yield put(actions.updateDeptFailure(get(error, 'message')));
  }
}

export function* deleteDept(api, action) {
  const response = yield call(api.deleteDept, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.deleteDeptSuccess(data));
  } catch (error) {
    yield put(actions.deleteDeptFailure(get(error, 'message')));
  }
}

export function* DepartmentSaga() {
  yield all([
    yield takeLatest(actions.getDepts.type, getDepts, api),
    yield takeLatest(actions.getDeptById.type, getDeptById, api),
    yield takeLatest(actions.createDept.type, createDept, api),
    yield takeLatest(actions.updateDept.type, updateDept, api),
    yield takeLatest(actions.deleteDept.type, deleteDept, api),
  ]);
}
