/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-fallthrough */
import React, { useEffect, useState, useMemo, useReducer } from 'react';
import * as _ from 'lodash';
import * as ConfigDeptSerSelector from 'store/configDeptService/shared/selectors';
import * as ConfigDeptSerlice from 'store/configDeptService/shared/slice';
import * as ConfigDeptSerConts from 'store/configDeptService/constants/configDeptService.constant';
import { ConfigDeptServiceSaga } from 'store/configDeptService/shared/saga';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { useForm } from 'react-hook-form';
import { Unsubscribe } from 'redux';
import { makeStyles } from '@material-ui/core/styles';
import { AppHelper, Order } from 'utils/app.helper';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { ConfigDeptSerAction } from '../components/ConfigDeptService/ConfigDeptSerAction';
import { TableGroups } from 'store/configDeptService/constants/configDeptService.constant';
import { RootStore } from 'store/configureStore';
import { rowsPerArray } from 'store/common/constants/common.constant';
import AppLoading from 'app/components/Loading';
import TablePagination from '@material-ui/core/TablePagination';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Tooltip from '@material-ui/core/Tooltip';
import TableHeaderSort from 'app/components/TableHeaderSort';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    height: '100%',
    maxHeight: '800px',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

const defaultPagi = {
  pageSize: 10,
  pageNumber: 1,
};

interface ModalState {
  openModal: boolean;
}

enum ModalType {
  OPEN = 'OPEN',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  openModal: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN:
      return {
        ...state,
        openModal: payload,
      };
    default:
      return state;
  }
};

export function ConfigDeptService() {
  const dispatch = useDispatch();
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  useInjectReducer({
    key: ConfigDeptSerlice.sliceKey,
    reducer: ConfigDeptSerlice.reducer,
  });
  useInjectSaga({
    key: ConfigDeptSerlice.sliceKey,
    saga: ConfigDeptServiceSaga,
  });
  const { register, handleSubmit, setValue } = useForm();
  const deptServices: any = useSelector(
    ConfigDeptSerSelector.selectDeptServices,
  );
  const loading = useSelector(ConfigDeptSerSelector.selectLoading);
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('name');
  const [pagi, setPagi] = useState(defaultPagi);
  const [{ openModal }, dispatchModal] = useReducer(
    reduceModal,
    initModalState,
  );

  useEffect(() => {
    dispatch(ConfigDeptSerlice.actions.getLoadListDeptServices(pagi));
    const rootStore$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload }: any = RootStore.getState().lastAction;
      switch (type) {
        case ConfigDeptSerlice.actions.getLoadListDeptServicesSuccess.type:
          dispatch(ConfigDeptSerlice.actions.getPerformanceRoom());
          break;
        case ConfigDeptSerlice.actions.getPerformanceRoomSuccess.type:
          dispatch(ConfigDeptSerlice.actions.getAllServices());
          break;
        case ConfigDeptSerlice.actions.getListDeptServicesSuccess.type:
          notifyController
            .setNotifyMessage(
              ConfigDeptSerConts.RESPONSE_CONFIG_DEPT_MESSAGE
                .GET_LIST_DEP_SERVICE_SUCCESS,
            )
            .setSuccessNotify();
          break;
        case ConfigDeptSerlice.actions.addDeptServicesInDeptSuccess.type:
          notifyController
            .setNotifyMessage(
              ConfigDeptSerConts.RESPONSE_CONFIG_DEPT_MESSAGE
                .CREATE_DEPT_SERVICE_SUCCESS,
            )
            .setSuccessNotify();
          renderListDeptService();
          break;
        case ConfigDeptSerlice.actions.updateDeptServicesInDeptSuccess.type:
          notifyController
            .setNotifyMessage(
              ConfigDeptSerConts.RESPONSE_CONFIG_DEPT_MESSAGE
                .UPDATE_DEPT_SERVICE_SUCCESS,
            )
            .setSuccessNotify();
          renderListDeptService();
          break;
        case ConfigDeptSerlice.actions.getDeptServicesByIdSuccess.type:
          dispatchModal({ type: ModalType.OPEN, payload: true });
          break;
        case ConfigDeptSerlice.actions.getListDeptServicesFail.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : ConfigDeptSerConts.RESPONSE_CONFIG_DEPT_MESSAGE
                    .GET_LIST_DEP_SERVICE_FAIL,
            )
            .setFailNotify();
          break;
        case ConfigDeptSerlice.actions.getDeptServicesByIdFail.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          break;
        case ConfigDeptSerlice.actions.addDeptServicesInDeptFail.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : ConfigDeptSerConts.RESPONSE_CONFIG_DEPT_MESSAGE
                    .CREATE_DEPT_SERVICE_FAIL,
            )
            .setFailNotify();
          break;
        case ConfigDeptSerlice.actions.updateDeptServicesInDeptFail.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : ConfigDeptSerConts.RESPONSE_CONFIG_DEPT_MESSAGE
                    .UPDATE_DEPT_SERVICE_FAIL,
            )
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      rootStore$();
    };
  }, []);

  const memoGroups = useMemo(() => {
    const { data = [], total_records, page_number, page_size } = deptServices;
    return {
      data,
      total: total_records,
      pageNumber: page_number,
      pageSize: page_size,
    };
  }, [deptServices]);

  const renderListDeptService = () => {
    dispatchModal({ type: ModalType.OPEN, payload: false });
    dispatch(ConfigDeptSerlice.actions.getListDeptServices(pagi));
  };

  const submitSearch = (values: any) => {
    dispatch(
      ConfigDeptSerlice.actions.getListDeptServices({
        ...values,
        ...pagi,
        pageNumber: 1,
      }),
    );
    setPagi({
      ...pagi,
      pageNumber: 1,
    });
  };

  const handleReset = () => {
    setValue('serviceCode', null);
    setValue('roomCode', null);
    setPagi(defaultPagi);
    dispatch(
      ConfigDeptSerlice.actions.getListDeptServices({
        pageNumber: 1,
        pageSize: 10,
      }),
    );
  };

  const renderTableControl = () => {
    return (
      <form onSubmit={handleSubmit(submitSearch)}>
        <div className="row">
          <div className="col-md-3">
            <input
              className="form-control mr-sm-2 w-100"
              type="serviceCode"
              name="serviceCode"
              placeholder="Tm theo m dch v"
              ref={register()}
            />
          </div>
          <div className="col-md-3">
            <input
              className="form-control mr-sm-2 w-100"
              type="roomCode"
              name="roomCode"
              placeholder="Tm theo m phng khm"
              ref={register()}
            />
          </div>
          <div className="col-md-3">
            <div className="btn-group">
              <button
                className="btn btn-outline-success btn__config mr-1"
                type="submit"
              >
                Tm kim <i className="fa fa-search"></i>
              </button>
            </div>
            <div className="btn-group">
              <button
                className="btn btn-outline-warning btn__config"
                type="button"
                onClick={handleReset}
              >
                Reset <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
          <div className="col-md-3 text-right">
            <div className="btn-group">
              <button
                className="btn btn-outline-success btn__config"
                type="button"
                onClick={() =>
                  dispatchModal({ type: ModalType.OPEN, payload: true })
                }
              >
                Thm phng khm dch v <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={TableGroups}
      />
    );
  };

  const renderTable = () => {
    if (!_.isEmpty(deptServices) && deptServices.data.length) {
      const tableData: any[] = AppHelper.stableSort(
        memoGroups.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, idx) => {
        const deptService = _.cloneDeep(row);
        return (
          <TableRow key={idx}>
            {TableGroups.map(column => {
              const value = deptService[column.id];
              if (column.id === 'no') {
                const { pageNumber, pageSize } = memoGroups;
                return (
                  <TableCell key={column.id}>
                    {pageNumber === 1
                      ? pageNumber + idx
                      : idx + 1 + (pageNumber - 1) * pageSize}
                  </TableCell>
                );
              }
              if (column.id === 'is_active') {
                return (
                  <TableCell key={column.id}>
                    <i
                      style={{
                        color: value ? '#4caf50' : '#ff0000',
                        fontSize: '16px',
                      }}
                      className={`fa fa-${value ? 'check' : 'times'}-circle`}
                      aria-hidden="true"
                    ></i>
                  </TableCell>
                );
              }
              if (column.id === 'actions') {
                return (
                  <TableCell key={column.id}>
                    <Tooltip title={'Sa'} enterDelay={500} leaveDelay={100}>
                      <button
                        className="btn btn-primary btn-xs"
                        onClick={() => {
                          dispatch(
                            ConfigDeptSerlice.actions.getDeptServicesById(
                              row.id,
                            ),
                          );
                        }}
                      >
                        <i className="fa fa-pencil"></i>
                      </button>
                    </Tooltip>
                  </TableCell>
                );
              }
              return (
                <TableCell key={column.id}>{value ? value : '-'}</TableCell>
              );
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={TableGroups.length}
          >
            Khng c d liu
          </TableCell>
        </TableRow>
      );
    }
  };

  const handleChangePage = (event: unknown, page: number) => {
    let changePage = Object.assign({}, pagi, {
      pageNumber: page + 1,
    });
    setPagi(changePage);
    dispatch(ConfigDeptSerlice.actions.getListDeptServices(changePage));
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, pagi, {
      pageSize: +event.target.value,
      pageNumber: 1,
    });
    setPagi(changeRowPerPage);
    dispatch(ConfigDeptSerlice.actions.getListDeptServices(changeRowPerPage));
  };

  const renderPagination = () => {
    if (!_.isEmpty(memoGroups) && memoGroups.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hin Th"
          count={memoGroups.total}
          rowsPerPage={memoGroups.pageSize}
          page={memoGroups.pageNumber - 1}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading && <AppLoading loading={true} />}
      {openModal && (
        <ConfigDeptSerAction
          open={openModal}
          onClose={() =>
            dispatchModal({ type: ModalType.OPEN, payload: false })
          }
        />
      )}
      <div className="col-md-12">
        <div className="card card-topline-red">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
