import { HttpRequest, configRequest } from 'utils/request';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import { ApiRoute } from '../constants/department.constant';

export class DepartmentHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEPT) {
    this.request = new HttpRequest(endPoint).request;
  }
  getDepts = (filter): Promise<any> =>
    this.request.get(ApiRoute.DEPT, configRequest(filter));

  getDeptById = (id: string): Promise<any> =>
    this.request.get(ApiRoute.DEPT + '/' + id);

  createDept = (dept: any): Promise<any> =>
    this.request.post(ApiRoute.DEPT, { ...configRequest(dept) });

  updateDept = ({ id, dept }): Promise<any> =>
    this.request.put(ApiRoute.DEPT + '/' + id, { ...configRequest(dept) });

  deleteDept = (id: string): Promise<any> =>
    this.request.delete(ApiRoute.DEPT + '/' + id);

  changeActive = ({ id, is_active }): Promise<any> => {
    return this.request.post(ApiRoute.DEPT_STATUS, { id, is_active });
  };
}
