/* eslint-disable @typescript-eslint/no-unused-vars */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from 'store/core/types';
import { initialState } from './slice';

const departmentService = (state: RootState) =>
  state.departmentService || initialState;

export const selectDeptServices = createSelector(
  [departmentService],
  state => state.deptServices,
);

export const selectDeptService = createSelector(
  [departmentService],
  state => state.deptService,
);

export const selectListValues = createSelector(
  [departmentService],
  state => state.deptServiceListValueId,
);

export const selectValueType = createSelector(
  [departmentService],
  state => state.deptServiceValueTypeId,
);

export const selectLoading = createSelector(
  [departmentService],
  state => state.loading,
);

export const selectError = createSelector(
  [departmentService],
  state => state.error,
);

export const selectSuccess = createSelector(
  [departmentService],
  state => state.success,
);

export const selectServices = createSelector(
  [departmentService],
  state => state.services,
);

export const selectListPerRoom = createSelector(
  [departmentService],
  state => state.listPerRoom,
);
