import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import { RoomApiRoutes } from '../constants/room.constants';
export class RoomHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.ENDPOINT) {
    this.request = new HttpRequest(endPoint).request;
  }

  public getRoomAll = () => {
    return this.request.get(RoomApiRoutes.GET_ALL);
  };

  public getRoomUltraSound = (): Promise<any> => {
    return this.request.get(RoomApiRoutes.GET_ROOM_ULTRA_SOUND);
  };

  public getRoomEndoscopy = (): Promise<any> => {
    return this.request.get(RoomApiRoutes.GET_ROOM_ENDOSCOPY);
  };

  public getCombineRoom = (): Promise<any> => {
    return Promise.all([this.getRoomUltraSound(), this.getRoomEndoscopy()]);
  };

  public changeActiveRoom = ({ id, is_active }): Promise<any> => {
    return this.request.post(RoomApiRoutes.CHANGE_ACTIVE_ROOM, {
      id,
      is_active,
    });
  };
}
