import { createSlice } from 'store/core/@reduxjs/toolkit';

export interface ConfigDeptServiceState {
  loading?: boolean;
  deptServices?: any;
  deptService?: any;
  serviceName?: any;
  deptServiceValueTypeId?: any;
  deptServiceListValueId?: any;
  listPerRoom: any;
  error?: any;
  success?: any;
  services: any;
}

export const initialState: ConfigDeptServiceState = {
  loading: false,
  deptServices: {},
  deptService: {},
  serviceName: {},
  deptServiceValueTypeId: '',
  deptServiceListValueId: '',
  listPerRoom: {},
  error: {},
  success: {},
  services: {},
};

const ConfigDeptService = createSlice({
  name: 'configDeptService',
  initialState,
  reducers: {
    getLoadListDeptServices(state, action) {
      state.loading = true;
    },

    getLoadListDeptServicesSuccess(state, action) {
      state.deptServices = action.payload;
      state.loading = false;
    },

    getLoadListDeptServicesFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getListDeptServices(state, action) {
      state.loading = true;
    },

    getListDeptServicesSuccess(state, action) {
      state.deptServices = action.payload;
      state.loading = false;
    },

    getListDeptServicesFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getPerformanceRoom(state) {
      state.loading = true;
    },

    getPerformanceRoomSuccess(state, action) {
      state.listPerRoom = action.payload;
      state.loading = false;
    },

    getPerformanceRoomFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getAllServices(state) {
      state.loading = true;
    },

    getAllServicesSuccess(state, action) {
      state.services = action.payload;
      state.loading = false;
    },

    getAllServicesFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getDeptServiceValueType(state) {
      state.loading = true;
    },

    getDeptServiceValueTypeSuccess(state, action) {
      state.loading = false;
      state.deptServiceValueTypeId = action.payload;
    },

    getDeptServiceValueTypeFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getDeptServiceListValue(state) {
      state.loading = true;
    },

    getDeptServiceListValueSuccess(state, action) {
      state.loading = false;
      state.deptServiceListValueId = action.payload;
    },

    getDeptServiceListValueFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    addDeptServicesInDept(state, action) {
      state.loading = true;
    },

    addDeptServicesInDeptSuccess(state, action) {
      state.loading = false;
    },

    addDeptServicesInDeptFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    updateDeptServicesInDept(state, action) {
      state.loading = true;
    },

    updateDeptServicesInDeptSuccess(state, action) {
      state.loading = false;
    },

    updateDeptServicesInDeptFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getDeptServicesById(state, action) {
      state.loading = true;
    },

    getDeptServicesByIdSuccess(state, action) {
      state.loading = false;
      state.deptService = action.payload;
    },

    getDeptServicesByIdFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    clearDeptService(state) {
      state.deptService = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = ConfigDeptService;
