/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect } from 'react';
import * as _ from 'lodash';
import * as TableSlice from 'store/table/shared/slice';
import * as SettingSelector from 'store/table/shared/selectors';
import { useInjectReducer, useInjectSaga } from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { TableSaga } from 'store/table/shared/saga';
import { useForm } from 'react-hook-form';
import { tableTypes } from 'store/table/constants/table.constant';
import { DEFAULT_ID } from 'store/common/constants/common.constant';
import { TableHttp } from 'store/table/services/table.http';
import { configResponse } from 'utils/request';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

export function ModalTable({ open, onClose }) {
  useInjectReducer({ key: TableSlice.sliceKey, reducer: TableSlice.reducer });
  useInjectSaga({ key: TableSlice.sliceKey, saga: TableSaga });
  const tableHttp = new TableHttp() ;
  const initTable: any = useSelector(SettingSelector.selectTableInfo);
  const { register, handleSubmit, errors, setError, setValue } = useForm();
  const dispatch = useDispatch();

  useEffect(() => {
    return () => {
      dispatch(TableSlice.actions.clearTableInfo());
    }
  }, []);

  useEffect(() => {
    async function initFormData(table: any) {
      for (let i in table) {
        const key = _.camelCase(i);
        if (key !== 'pcIP' || key !== 'deviceType' || key !== 'device') {
          setValue(key, table[i]);
        } 
        setValue('deviceCode', table['device']);
        setValue('computerIP', table['pc_ip']);
        setValue('deviceType', table['device_type']);
      }
    }
    if (!_.isEmpty(initTable)) {
      setTimeout(() => initFormData(initTable));
    }
  }, [initTable]);

  const onSubmit = async (tableInfo: any) => {
    const msg = 'M bn tip nhn  tn ti';
    try {
      let checkCodeRes: any = {};
      if (_.isEmpty(initTable) || initTable.code !== tableInfo.code) {
        const checkCode: any = await tableHttp.checkUniqueTable({
          id: DEFAULT_ID,
          code: tableInfo.code,
        });
        checkCodeRes = await configResponse(checkCode);
      }
      if (_.isBoolean(checkCodeRes)) {
        throw new Error(msg);
      }
      if (_.isEmpty(initTable)) {
        dispatch(TableSlice.actions.createTable(tableInfo));
      } else {
        dispatch(TableSlice.actions.updateTable({ table: tableInfo, id: initTable.id }))
      }
    } catch (err) {
      const errMsg = _.get(err, 'message');
      return setError('code', {
        type: 'exist',
        message: errMsg ? errMsg : msg,
      });
    }
  }

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title">Thm Mi thng tin bn tip nhn</h4>
          <div className="form-group">
            <label htmlFor="name">
                Tn
              </label>
              <input
                id="name"
                type="text"
                className="form-control Name"
                name="name"
                ref={register({ required: true })}
                placeholder="Tn" autoComplete="off" />
                {errors.name && errors.name?.type === 'required' ? (
                  <span style={{ color: ' #fb0606' }}>Vui lng nhp tn</span>
                ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="code">
                M
              </label>
              <input
                id="code"
                type="text"
                className="form-control Code"
                name="code"
                ref={register({ required: true })}
                placeholder="M" autoComplete="off" />
                {errors.code && errors.code?.type === 'required' && (
                  <span style={{ color: ' #fb0606' }}>Vui lng nhp m</span>
                )}
                {errors.code && errors.code?.type === 'exist' && (
                  <span style={{ color: ' #fb0606' }}>{ errors?.code?.message ? errors.code.message : 'Error' }</span>
                )}
          </div>
          <div className="form-group">
            <label htmlFor="device">
                Thit b
              </label>
              <input
                id="device"
                type="text"
                name="deviceCode"
                ref={register({ required: true })}
                className="form-control DeviceCode"
                placeholder="Thit b" autoComplete="off" />
                {errors.deviceCode && errors.deviceCode?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng nhp thit b</span>
              ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="table-type">
              Loi bn
            </label>
            <select
              id="table-type"
              className="form-control"
              name="type"
              ref={register({ required: true })}
              >
                <option value="">Loi bn</option>
                { tableTypes.map(({ value, label }) => (
                  <option key={value} value={value}>{ label }</option>
                ))}
              </select>
              {errors.type && errors.type?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn loi bn</span>
              ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="table">
              Loi thit b
            </label>
            <select id="table"
              className="form-control"
              name="deviceType"
              ref={register({ required: true })}
              >
              <option value="">Loi Thit B</option>
              <option value="0">Bn</option>
              <option value="1">CPS</option>
            </select>
            {errors.device_type && errors.device_type?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn loi thit B</span>
              ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="pc-ip">
              PC IP
            </label>
            <input
              type="text"
              className="form-control"
              placeholder="PC IP"
              name="computerIP"
              ref={register({ required: true })}
            />
             {errors.computerIP && errors.computerIP?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng nhp PC IP</span>
              ) : null}
          </div>
          <div className="form-group">
            <label htmlFor="table">
              M Khu
            </label>
            <select id="areaCode" className="form-control DeviceType" autoComplete="off" 
              name="areaCode"
              ref={register({ required: true })}>
              <option value="">M Khu</option>
              <option value="B">Khu B</option>
              <option value="D">Khu D</option>
            </select>
            {errors.areaCode && errors.areaCode?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn loi m khu</span>
              ) : null}
          </div>
          <div className="form-group d-flex justify-content-end">
            <button type="button" className="btn btn-secondary" onClick={onClose}>ng</button>
            <button type="submit" className="btn btn-primary ml-2">Lu</button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
