/* eslint-disable no-useless-concat */
import { HttpRequest } from 'utils//request';
import {
  APP_CONSTANT,
  valuePerRoom,
} from 'store/common/constants/common.constant';
import { ApiRoutes } from '../constants/departmentService.constant';

export class DepartmentServiceHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEPT) {
    this.request = new HttpRequest(endPoint).request;
  }
  public getDeptServiceValueType = (): Promise<any> => {
    return this.request.get(ApiRoutes.VALUE_TYPE + '/DV');
  };

  public getPerformanceRoom = (): Promise<any> => {
    return this.request.get(ApiRoutes.PERFORMANCE_ROOM + valuePerRoom);
  };

  public getDeptServiceListValue = (): Promise<any> => {
    return this.request.get(ApiRoutes.LIST_VALUE + '/NDV');
  };

  public getListDeptServices = ({
    serviceCode,
    roomCode,
    pageSize,
    pageNumber,
    examinationCode,
  }): Promise<any> => {
    return this.request.get(ApiRoutes.DEPT_SERVICE, {
      serviceCode,
      roomCode,
      examinationCode,
      page_number: pageNumber,
      page_size: pageSize,
    });
  };

  public getDeptServicesById = (id: any): Promise<any> => {
    return this.request.get(
      `${ApiRoutes.DEPT_GET_ID_SERVICE}/${id}` + '/services',
    );
  };

  public addDeptServicesInDept = (deptService): Promise<any> => {
    return this.request.put(ApiRoutes.CREATE_DEPT_SERVICE, deptService);
  };

  public updateDeptServicesInDept = (reqquest): Promise<any> => {
    return this.request.put(ApiRoutes.UPDATE_DEPT_SERVICE, reqquest);
  };

  getAllServices = (): Promise<any> => {
    return this.request.get(ApiRoutes.GET_ALL_SERVICES);
  };
}
