/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useMemo } from 'react';
import * as _ from 'lodash';
import * as DeptGroupSelector from 'store/departmentGroup/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { sliceKey, reducer, actions } from 'store/departmentGroup/shared/slice';
import { AppHelper, Order } from 'utils/app.helper';
import { DeptGroupSaga } from 'store/departmentGroup/shared/saga';
import { makeStyles } from '@material-ui/core/styles';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { Tooltip } from '@material-ui/core';
import { DeptGroupActions } from '../components/DepartmentGroup/DeptGroupActions';
import { ModalConfirm } from 'app/components/Modal/Confirm';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { useForm } from 'react-hook-form';
import { rowsPerArray } from 'store/common/constants/common.constant';
import TablePagination from '@material-ui/core/TablePagination';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import AppLoading from 'app/components/Loading';
import TableHeaderSort from 'app/components/TableHeaderSort';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  td: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
  },
  pagination: {},
});

export const tableValue: any[] = [
  { id: 'code', label: 'M nhm phng khm', minWidth: 120 },
  { id: 'description', label: 'Tn nhm phng khm', minWidth: 120 },
  { id: 'actions', label: 'Chc nng', minWidth: 270 },
];

const defaultPagi: any = {
  pageNumber: 1,
  pageSize: 10,
};

export const CREATE_DEPT_GROUP_SUCCESS = 'To mi nhm phng khm thnh cng';
export const CREATE_DEPT_GROUP_FAIL = 'To mi nhm phng khm tht bi';
export const UPDATE_DEPT_GROUP_SUCCESS = 'Cp nht nhm phng khm thnh cng';
export const UPDATE_DEPT_GROUP_FAIL = 'Cp nht nhm phng khm tht bi';
export const DELETE_DEPT_GROUP_SUCCESS = 'Xo nhm phng khm thnh cng';
export const DELETE_DEPT_GROUP_FAIL = 'Xo nhm phng khm tht bi';
export const ADD_GROUP_SUCCESS = 'Thm phng khm vo nhm thnh cng';
export const ADD_GROUP_FAIL = 'Thm phng khm vo nhm tht bi';

export function DepartmentGroup() {
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  useInjectReducer({
    key: sliceKey,
    reducer: reducer,
  });
  useInjectSaga({ key: sliceKey, saga: DeptGroupSaga });
  const classes = useStyles();
  const dispatch = useDispatch();
  const groups: any = useSelector(DeptGroupSelector.selectGroups);
  const loading: any = useSelector<any>(DeptGroupSelector.selectLoading);
  const [openModal, setOpenModal] = useState<boolean>(false);
  const [openDelete, setOpenDelete] = useState<boolean>(false);
  const [idDelete, setIdDelete] = useState<string>('');
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('code');
  const [pagi, setPagi] = useState(defaultPagi);
  const { register, handleSubmit, setValue } = useForm();

  useEffect(() => {
    dispatch(actions.getGroups(defaultPagi));
    const rootStore$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload }: any = RootStore.getState().lastAction;
      switch (type) {
        case actions.getGroupByIdSuccess.type:
          setOpenModal(true);
          break;
        case actions.addDeptToGroupSuccess.type:
          regetListGroup();
          break;
        case actions.createGroupSuccess.type:
          notifyController
            .setNotifyMessage(CREATE_DEPT_GROUP_SUCCESS)
            .setSuccessNotify();
          break;
        case actions.updateGroupSuccess.type:
          notifyController
            .setNotifyMessage(UPDATE_DEPT_GROUP_SUCCESS)
            .setSuccessNotify();
          break;
        case actions.deleteGroupSuccess.type:
          notifyController
            .setNotifyMessage(DELETE_DEPT_GROUP_SUCCESS)
            .setSuccessNotify();
          regetListGroup();
          break;
        case actions.createGroupFailure.type:
          notifyController
            .setNotifyMessage(payload ? payload : CREATE_DEPT_GROUP_FAIL)
            .setFailNotify();
          break;
        case actions.updateGroupFailure.type:
          notifyController
            .setNotifyMessage(payload ? payload : UPDATE_DEPT_GROUP_FAIL)
            .setFailNotify();
          break;
        case actions.deleteGroupFailure.type:
          notifyController
            .setNotifyMessage(payload ? payload : DELETE_DEPT_GROUP_FAIL)
            .setFailNotify();
          regetListGroup();
          break;
        case actions.addDeptToGroupFailure.type:
          notifyController
            .setNotifyMessage(payload ? payload : ADD_GROUP_FAIL)
            .setFailNotify();
          break;
      }
    });
    return () => {
      rootStore$();
    };
  }, []);

  const regetListGroup = () => {
    dispatch(actions.getGroups(pagi));
    setOpenModal(false);
    setIdDelete('');
    setOpenDelete(false);
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const deleteGroup = () => {
    if (idDelete === '') return;
    dispatch(actions.deleteGroup(idDelete));
  };

  const submitSearch = (values: any) => {
    dispatch(
      actions.getGroups({
        ...values,
        ...pagi,
        pageNumber: 1,
      }),
    );
    setPagi({
      ...pagi,
      pageNumber: 1,
    });
  };

  const handleReset = () => {
    setValue('code', null);
    setValue('description', null);
    setPagi(defaultPagi);
    dispatch(
      actions.getGroups({
        pageNumber: 1,
        pageSize: 10,
      }),
    );
  };

  const renderTableControl = () => {
    return (
      <form className="form-inline w-100" onSubmit={handleSubmit(submitSearch)}>
        <div className="row w-100">
          <div className="col-md-3 col-xl-2">
            <input
              className="form-control mr-sm-2 w-100"
              type="text"
              name="code"
              placeholder="Tm theo m nhm phng khm"
              ref={register()}
            />
          </div>
          <div className="col-md-3 col-xl-2">
            <input
              className="form-control mr-sm-2 w-100"
              type="text"
              name="description"
              placeholder="Tm theo tn nhm phng khm"
              ref={register()}
            />
          </div>
          <div className="col-md-6 col-xl-8 d-flex">
            <div className="btn-group mr-2">
              <button
                className="btn btn-outline-success btn__config"
                type="submit"
              >
                Tm kim <i className="fa fa-search"></i>
              </button>
            </div>
            <div className="btn-group mr-2">
              <button
                className="btn btn-primary btn__config mb-0"
                type="button"
                onClick={() => setOpenModal(true)}
              >
                Thm nhm phng khm <i className="fa fa-plus"></i>
              </button>
            </div>
            <div className="btn-group">
              <button
                className="btn btn-warning btn__config mb-0"
                type="button"
                onClick={handleReset}
              >
                Reset <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tableValue}
      />
    );
  };

  const renderData = useMemo(() => {
    const { data = [], total_records, page_number, page_size } = groups;
    return {
      data,
      total: total_records,
      pageNumber: page_number,
      pageSize: page_size,
    };
  }, [groups]);

  const renderTable = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      const tableData: any[] = AppHelper.stableSort(
        renderData.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, idx) => {
        const group = _.cloneDeep(row);
        return (
          <TableRow key={row.id}>
            {tableValue.map(column => {
              const value = group[column.id];
              if (column.id === 'actions') {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    <Tooltip title={'Sa'} enterDelay={500} leaveDelay={100}>
                      <button
                        className="btn btn-info btn-xs"
                        onClick={() => {
                          dispatch(actions.getGroupById(group.id));
                        }}
                      >
                        <i className="fa fa-pencil"></i>
                      </button>
                    </Tooltip>
                    <Tooltip title={'Xo'} enterDelay={500} leaveDelay={100}>
                      <button
                        className="btn btn-danger btn-xs mx-2"
                        onClick={() => {
                          setIdDelete(group.id);
                          setOpenDelete(true);
                        }}
                      >
                        <i className="fa fa-trash-o"></i>
                      </button>
                    </Tooltip>
                  </TableCell>
                );
              }
              return (
                <TableCell key={column.id} className={classes.td}>
                  {value ? value : '-'}
                </TableCell>
              );
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tableValue.length}
          >
            No matching records found
          </TableCell>
        </TableRow>
      );
    }
  };

  const handleChangePage = (event: unknown, page: number) => {
    let changePage = Object.assign({}, pagi, {
      pageNumber: page + 1,
    });
    setPagi(changePage);
    dispatch(actions.getGroups(changePage));
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, pagi, {
      pageSize: +event.target.value,
      pageNumber: 1,
    });
    setPagi(changeRowPerPage);
    dispatch(actions.getGroups(changeRowPerPage));
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hin Th"
          count={renderData.total}
          rowsPerPage={renderData.pageSize}
          page={renderData.pageNumber - 1}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {openModal && (
        <DeptGroupActions
          open={true}
          onClose={() => {
            setOpenModal(false);
          }}
        />
      )}
      <ModalConfirm
        title={'Bn c chc chn mun xo loi phng khm ny?'}
        other={{
          bodyText: '',
        }}
        keyConfirm={''}
        open={openDelete}
        onClose={() => setOpenDelete(false)}
        confirmMethod={() => deleteGroup()}
      />
      {loading ? <AppLoading loading={true} /> : null}
      <div className="col-md-12">
        <div className="card card-topline-color-default">
          <div className="card-head"></div>
          <div className="card-body ">
            {renderTableControl()}
            <div className="table-scrollable">
              <Paper className={classes.root}>
                <TableContainer className={classes.container}>
                  <Table stickyHeader>
                    <TableHead>{renderColumns()}</TableHead>
                    <TableBody>{renderTable()}</TableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
