/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as ConfigDeptSerSelector from 'store/configDeptService/shared/selectors';
import * as ConfigDeptSerlice from 'store/configDeptService/shared/slice';
import { ConfigDeptServiceSaga } from 'store/configDeptService/shared/saga';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { useForm, Controller } from 'react-hook-form';
import Checkbox from '@material-ui/core/Checkbox';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import ListSubheader from '@material-ui/core/ListSubheader';
import { useTheme } from '@material-ui/core/styles';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import { VariableSizeList, ListChildComponentProps } from 'react-window';
import TekMultiSelect from 'app/components/MultiSelect';
import {
  areaClinci,
  typePatient,
} from 'store/departmentService/constants/departmentService.constant';

const LISTBOX_PADDING = 8;

function renderRow(props: ListChildComponentProps) {
  const { data, index, style } = props;
  return React.cloneElement(data[index], {
    style: {
      ...style,
      top: (style.top as number) + LISTBOX_PADDING,
    },
  });
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef<HTMLDivElement>((props, ref) => {
  const outerProps = React.useContext(OuterElementContext);
  return <div ref={ref} {...props} {...outerProps} />;
});

function useResetCache(data: any) {
  const ref = React.useRef<VariableSizeList>(null);
  React.useEffect(() => {
    if (ref.current != null) {
      ref.current.resetAfterIndex(0, true);
    }
  }, [data]);
  return ref;
}

const ListboxComponent = React.forwardRef<HTMLDivElement>(
  function ListboxComponent(props, ref) {
    const { children, ...other } = props;
    const itemData = React.Children.toArray(children);
    const theme = useTheme();
    const smUp = useMediaQuery(theme.breakpoints.up('sm'), { noSsr: true });
    const itemCount = itemData.length;
    const itemSize = smUp ? 36 : 48;

    const getChildSize = (child: React.ReactNode) => {
      if (React.isValidElement(child) && child.type === ListSubheader) {
        return 48;
      }

      return itemSize;
    };

    const getHeight = () => {
      if (itemCount > 8) {
        return 8 * itemSize;
      }
      return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
    };

    const gridRef = useResetCache(itemCount);

    return (
      <div ref={ref}>
        <OuterElementContext.Provider value={other}>
          <VariableSizeList
            itemData={itemData}
            height={getHeight() + 2 * LISTBOX_PADDING}
            width="100%"
            ref={gridRef}
            outerElementType={OuterElementType}
            innerElementType="ul"
            itemSize={index => getChildSize(itemData[index])}
            overscanCount={5}
            itemCount={itemCount}
          >
            {renderRow}
          </VariableSizeList>
        </OuterElementContext.Provider>
      </div>
    );
  },
);

const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxIcon fontSize="small" />;

const valueEditType = { value: '', label: '' };
const valueEditSer = { examination_code: '', examination_name: '' };

export function ConfigDeptSerAction({ open, onClose }) {
  const dispatch = useDispatch();
  useInjectReducer({
    key: ConfigDeptSerlice.sliceKey,
    reducer: ConfigDeptSerlice.reducer,
  });
  useInjectSaga({
    key: ConfigDeptSerlice.sliceKey,
    saga: ConfigDeptServiceSaga,
  });
  const services: any[] = useSelector(ConfigDeptSerSelector.selectServices);
  const deptService: any = useSelector(ConfigDeptSerSelector.selectDeptService);
  const listPerRoom: any = useSelector(ConfigDeptSerSelector.selectListPerRoom);
  const [selectedType, setSelectedType] = useState<any[]>([]);
  const [selectedEditType, setSelectedEditType] = useState<any>(valueEditType);
  const [selectedEditSer, setSelectedEditSer] = useState<any>(valueEditSer);
  const [errorEditType, setErrorEditType] = useState<boolean>(false);
  const [errorEditSer, setErrorEditSer] = useState<boolean>(false);
  const [desgRoom, setDesRoom] = useState<any[]>([]);
  const {
    handleSubmit,
    errors,
    control,
    clearErrors,
    setValue,
    setError,
    register,
  } = useForm();

  useEffect(() => {
    return () => {
      dispatch(ConfigDeptSerlice.actions.clearDeptService());
    };
  }, []);

  useEffect(() => {
    if (_.isEmpty(deptService)) return;
    function initFormData(deptService) {
      if (!_.isEmpty(deptService)) {
        const {
          room_code,
          room_name,
          examination_code,
          examination_name,
        } = deptService;
        setValue('room_code', {
          code: room_code,
          name: room_name,
        });
        setValue('examination_code', {
          code: examination_code,
          name: examination_name,
        });
      }
    }
    setTimeout(() => initFormData(deptService));
  }, [deptService]);

  const onSubmit = (values: any) => {
    if (_.isEmpty(deptService)) {
      let service_code_list: any[] = values['service_code_list'];
      let object_type_list: any[] = selectedType.map(item => item.value);
      if (!service_code_list.length) {
        return setError('service_code_list', { type: 'required' });
      }
      if (!object_type_list.length) {
        return setError('type_patient', { type: 'required' });
      }
      service_code_list = service_code_list.map(({ code }) => code);
      const reqquest = {
        examination_code: _.get(values, 'examination_code.code'),
        room_code: _.get(values, 'room_code.code'),
        service_code_list,
        object_type_list,
      };
      return dispatch(
        ConfigDeptSerlice.actions.addDeptServicesInDept(reqquest),
      );
    }
    if (!_.isEmpty(deptService)) {
      if (errorEditType || errorEditSer) {
        return;
      }
      const reqquest = {
        examination_code: _.get(values, 'examination_code.code'),
        room_code: _.get(values, 'room_code.code'),
        service_code: _.isEmpty(selectedEditSer.examination_code)
          ? deptService.service_code
          : selectedEditSer.examination_code,
        object_type: _.isEmpty(selectedEditType.value)
          ? deptService.object_type
          : selectedEditType.value,
      };
      return dispatch(
        ConfigDeptSerlice.actions.updateDeptServicesInDept({
          reqquest,
          id: deptService.id,
        }),
      );
    }
  };

  const handleToggleType = selectedOptions => {
    clearErrors('type_patient');
    setValue('type_patient', selectedOptions);
    setSelectedType(selectedOptions);
  };
  const handleClearType = () => setSelectedType([]);
  const handleSelectTypeAll = isSelected => {
    if (isSelected) {
      setValue('type_patient', isSelected);
      clearErrors('type_patient');
      setSelectedType(typePatient);
    } else {
      handleClearType();
    }
  };

  const handledesRoom = e => {
    setValue('examination_code', null);
    let shortDepts: any[] = [];
    if (
      !_.isEmpty(listPerRoom) &&
      listPerRoom.data.length &&
      _.isEmpty(e.target.value)
    ) {
      setDesRoom(listPerRoom.data);
    } else if (
      !_.isEmpty(listPerRoom) &&
      listPerRoom.data.length &&
      !_.isEmpty(e.target.value)
    ) {
      shortDepts = listPerRoom.data.filter(
        item => item.area_code === e.target.value,
      );
      setDesRoom(shortDepts);
    }
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'sm'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
      disableBackdropClick
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <h4 className="modal-title pb-3 font-weight-bold">
            {_.isEmpty(deptService) ? 'Thm dch v' : 'Cp nht dch vu'}
          </h4>
          <div className="form-group">
            <label>Loi bnh nhn</label>
            {_.isEmpty(deptService) ? (
              <Controller
                name="type_patient"
                control={control}
                rules={{ required: true }}
                defaultValue={null}
                render={({ field, ...input }: any) => {
                  return (
                    <TekMultiSelect
                      items={typePatient}
                      getOptionLabel={option => `${option.label}`}
                      selectedValues={selectedType}
                      label=""
                      placeholder="Chn loi bnh nhn"
                      selectAllLabel="Tt c"
                      onToggleOption={handleToggleType}
                      onClearOptions={handleClearType}
                      onSelectAll={handleSelectTypeAll}
                    />
                  );
                }}
              />
            ) : (
              <Autocomplete
                options={typePatient}
                defaultValue={typePatient.find(
                  ({ value }) => value === deptService.object_type,
                )}
                getOptionLabel={option => option.label}
                renderInput={params => (
                  <TextField
                    {...params}
                    variant="outlined"
                    placeholder="Chn loi bnh nhn"
                  />
                )}
                renderOption={(option, { selected }) => {
                  return (
                    <React.Fragment>
                      <Checkbox
                        icon={icon}
                        checkedIcon={checkedIcon}
                        style={{ marginRight: 8 }}
                        checked={selected}
                        defaultChecked
                        color="primary"
                      />
                      {option.label}
                    </React.Fragment>
                  );
                }}
                onChange={(e, code) => {
                  if (_.isEmpty(code)) {
                    setErrorEditType(true);
                  }
                  if (!_.isEmpty(code)) {
                    setErrorEditType(false);
                    setSelectedEditType(code);
                  }
                }}
              />
            )}
            {errors.type_patient &&
              errors.type_patient?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn loi bnh nhn
                </span>
              )}
            {errorEditType && (
              <span style={{ color: ' #fb0606' }}>
                Vui lng chn loi bnh nhn
              </span>
            )}
          </div>
          <div className="form-group">
            <label htmlFor="area_code">Khu phng khm</label>
            <select
              id="area_code"
              className="form-control"
              name="area_code"
              ref={register()}
              placeholder="Nhp phng khm"
              onChange={handledesRoom}
            >
              <option>Chn khu phng khm</option>
              <option value="">Tt c</option>
              {areaClinci.map(({ code, label }) => (
                <option key={code} value={code}>
                  {label}
                </option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label htmlFor="examination_code">Phng ch nh</label>
            <Controller
              name="examination_code"
              control={control}
              rules={{ required: true }}
              defaultValue={null}
              render={({ field, ...input }: any) => {
                return (
                  <Autocomplete
                    value={input.value}
                    disableListWrap
                    options={
                      !_.isEmpty(desgRoom) && desgRoom.length ? desgRoom : []
                    }
                    getOptionLabel={option => option.name}
                    renderInput={params => (
                      <TextField
                        {...params}
                        variant="outlined"
                        placeholder="Phng ch nh"
                      />
                    )}
                    onChange={(e, value) => {
                      clearErrors('examination_code');
                      setValue('examination_code', value);
                    }}
                  />
                );
              }}
            />
            {errors.examination_code &&
              errors.examination_code?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn phng ch nh
                </span>
              )}
          </div>
          <div className="form-group">
            <label htmlFor="code">Phng thc hin</label>
            <Controller
              name="room_code"
              control={control}
              rules={{ required: true }}
              defaultValue={null}
              render={({ field, ...input }: any) => {
                return (
                  <Autocomplete
                    value={input.value}
                    disableListWrap
                    options={
                      !_.isEmpty(listPerRoom) && listPerRoom.data.length
                        ? listPerRoom.data
                        : []
                    }
                    getOptionLabel={option => option?.name}
                    renderInput={params => (
                      <TextField
                        {...params}
                        variant="outlined"
                        placeholder="Phng thc hin"
                      />
                    )}
                    onChange={(e, value) => {
                      clearErrors('room_code');
                      setValue('room_code', value);
                    }}
                  />
                );
              }}
            />
            {errors.room_code && errors.room_code?.type === 'required' && (
              <span style={{ color: ' #fb0606' }}>
                Vui lng chn phng thc hin
              </span>
            )}
          </div>
          <div className="form-group">
            <label htmlFor="services" className="mb-3">
              Chn dch v
            </label>
            {_.isEmpty(deptService) ? (
              <React.Fragment>
                <Controller
                  name="service_code_list"
                  control={control}
                  rules={{ required: true }}
                  render={({ field, ...input }: any) => {
                    return (
                      <Autocomplete
                        options={!_.isEmpty(services) ? services : []}
                        value={input.value}
                        disableCloseOnSelect
                        disableListWrap
                        multiple
                        limitTags={2}
                        getOptionLabel={option => option?.description}
                        ListboxComponent={
                          ListboxComponent as React.ComponentType<
                            React.HTMLAttributes<HTMLElement>
                          >
                        }
                        renderInput={params => (
                          <TextField
                            {...params}
                            variant="outlined"
                            placeholder="Chn dch v"
                          />
                        )}
                        renderOption={(option, { selected }) => {
                          return (
                            <React.Fragment>
                              <Checkbox
                                icon={icon}
                                checkedIcon={checkedIcon}
                                style={{ marginRight: 8 }}
                                checked={selected}
                                color="primary"
                              />
                              {option.description}
                            </React.Fragment>
                          );
                        }}
                        onChange={(e, value) => {
                          setValue('service_code_list', value);
                          clearErrors('service_code_list');
                        }}
                      />
                    );
                  }}
                />
                {errors.service_code_list &&
                  errors.service_code_list?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>
                      Vui lng chn dch v
                    </span>
                  )}
              </React.Fragment>
            ) : (
              <React.Fragment>
                <Autocomplete
                  options={!_.isEmpty(services) ? services : []}
                  defaultValue={
                    !_.isEmpty(services)
                      ? services.find(
                          ({ code }) => code === deptService.service_code,
                        )
                      : null
                  }
                  getOptionLabel={option => option?.description}
                  ListboxComponent={
                    ListboxComponent as React.ComponentType<
                      React.HTMLAttributes<HTMLElement>
                    >
                  }
                  renderInput={params => (
                    <TextField
                      {...params}
                      variant="outlined"
                      placeholder="Chn dch v"
                    />
                  )}
                  renderOption={(option, { selected }) => {
                    return (
                      <React.Fragment>
                        <Checkbox
                          icon={icon}
                          checkedIcon={checkedIcon}
                          style={{ marginRight: 8 }}
                          checked={selected}
                          defaultChecked
                          color="primary"
                        />
                        {option.description}
                      </React.Fragment>
                    );
                  }}
                  onChange={(e, value) => {
                    if (_.isEmpty(value)) {
                      setErrorEditSer(true);
                    }
                    if (!_.isEmpty(value)) {
                      setErrorEditSer(false);
                      setSelectedEditSer(value);
                    }
                  }}
                />
                {errorEditSer && (
                  <span style={{ color: ' #fb0606' }}>
                    Vui lng chn dch v
                  </span>
                )}
              </React.Fragment>
            )}
          </div>
          <div className="form-group d-flex justify-content-end py-3 mb-0">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              ng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              Lu
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
