export function* DeptGroupSaga() {
  yield all([
    yield takeLatest(actions.createGroup.type, createGroup, api),
    yield takeLatest(actions.addDeptToGroup.type, addDeptToGroup, api),
    yield takeLatest(actions.getDeptInGroup.type, getDeptInGroup, api),
    yield takeLatest(actions.deleteGroup.type, deleteGroup, api),
    yield takeLatest(actions.updateGroup.type, updateGroup, api),
    yield takeLatest(actions.getGroupById.type, getGroupById, api),
    yield takeLatest(actions.getGroupByCode.type, getGroupByCode, api),
    yield takeLatest(actions.getGroups.type, getGroups, api),
  ]);
}
