import { flatten } from 'lodash';
import { all, call, put, takeLatest } from 'redux-saga/effects';
import { RoomHttp } from '../services/room.http';
import { actions } from './slice';
const roomRequest = new RoomHttp();
const roomRequestUltraSound = new RoomHttp();

export function* getRoomAll(api) {
  const response = yield call(api.getRoomAll);
  try {
    if (response.ok) {
      yield put(actions.getRoomAllSuccess(response.data.result.data));
    } else {
      yield put(actions.getRoomAllFailure(response.data));
    }
  } catch (err) {
    yield put(actions.getRoomAllFailure(err));
  }
}

export function* getRoomUltraSound(api) {
  const response = yield call(api.getRoomUltraSound);
  try {
    if (response.ok) {
      yield put(actions.getRoomUltraSoundSuccess(response.data.result));
    } else {
      yield put(actions.getRoomUltraSoundFailure(response.data));
    }
  } catch (err) {
    yield put(actions.getRoomUltraSoundFailure(err));
  }
}

export function* getRoomEndoscopy(api) {
  const response = yield call(api.getRoomEndoscopy);
  try {
    if (response.ok) {
      yield put(actions.getRoomEndoscopySuccess(response.data.result));
    } else {
      yield put(actions.getRoomEndoscopyFailure(response.data));
    }
  } catch (err) {
    yield put(actions.getRoomEndoscopyFailure(err));
  }
}

export function* getCombineRoom(api) {
  const response: any[] = yield call(api.getCombineRoom);
  try {
    const success: boolean = response.every(({ ok }: any) => ok);
    const data: any[] = flatten(
      response.map(({ data: { result } }: any) => result),
    );
    if (success) {
      yield put(actions.getCombineRoomSuccess(data));
    } else {
      yield put(actions.getCombineRoomFailure(response));
    }
  } catch (err) {
    yield put(actions.getCombineRoomFailure(err));
  }
}

export function* RoomSaga() {
  yield all([
    yield takeLatest(actions.getRoomAll.type, getRoomAll, roomRequest),
    yield takeLatest(
      actions.getRoomUltraSound.type,
      getRoomUltraSound,
      roomRequestUltraSound,
    ),
    yield takeLatest(
      actions.getRoomEndoscopy.type,
      getRoomEndoscopy,
      roomRequest,
    ),
    yield takeLatest(actions.getCombineRoom.type, getCombineRoom, roomRequest),
  ]);
}
