/* eslint-disable @typescript-eslint/no-unused-vars */
import { DeptGroupApiRoutes } from '../constants';
import { HttpRequest, configRequest } from 'utils/request';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import * as _ from 'lodash';
import { isEmpty } from 'lodash';
export class DeptGroupHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.DEPT) {
    this.request = new HttpRequest(endPoint).request;
  }

  getGroups = (filter): Promise<any> => {
    return this.request.get(
      DeptGroupApiRoutes.GROUP,
      _.isEmpty(filter) ? {} : configRequest(filter),
    );
  };

  createGroup = (group: any): Promise<any> =>
    this.request.post(DeptGroupApiRoutes.GROUP, configRequest(group));

  getGroupById = (id: string): Promise<any> =>
    this.request.get(DeptGroupApiRoutes.GROUP + '/' + id);

  getGroupByCode = (code: string): Promise<any> =>
    this.request.get(DeptGroupApiRoutes.GROUP_DEPT + '/' + code);

  updateGroup = ({ id, groupCode, groupName }): Promise<any> => {
    return this.request.put(DeptGroupApiRoutes.GROUP + '/' + id, {
      group_code: groupCode,
      group_name: groupName,
    });
  };

  deleteGroup = (id: string): Promise<any> =>
    this.request.delete(DeptGroupApiRoutes.GROUP + '/' + id);

  addDeptToGroup = ({ id, depts, departmentVirtualCode }): Promise<any> => {
    return this.request.put(DeptGroupApiRoutes.GROUP_DEPT + '/' + id, {
      department_code: depts,
      department_virtual_code: departmentVirtualCode.code,
    });
  };

  getDeptInGroup = (code: string): Promise<any> =>
    this.request.get(DeptGroupApiRoutes.GROUP_DEPT + '/' + code);
}
